import React from "react";
import { Paper, Typography, Grid } from "@material-ui/core";
import { green, pink } from "@material-ui/core/colors";
import { NotificationManager } from "react-notifications";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import makeAnimated from "react-select/animated";
import Select from "react-select";
import OrderTransferItem from "../../components/orderTransfer/orderTransferListItem/orderTransferItem";
import List from "@material-ui/core/List";
import Button from "@material-ui/core/Button";
import { useDispatch } from "react-redux";
import LoadingOverlay from "react-loading-overlay";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import useMediaQuery from "@material-ui/core/useMediaQuery";
import DescriptionIcon from "@material-ui/icons/Description";
import Fab from "@material-ui/core/Fab";
import { CSVLink } from "react-csv";
import { serverIP, monthNames } from "../../util/Constants";
const animatedComponents = makeAnimated();

const useStyles = makeStyles((theme) => ({
    paper: {
        padding: "20px",
    },
    field: {
        //width: '200px'
    },
    greenAvatar: {
        marginRight: "10px",
        color: "#fff",
        backgroundColor: green[500],
    },
    root: {
        width: "100%",

        backgroundColor: theme.palette.background.paper,
        position: "relative",
        overflow: "auto",
        maxHeight: "850px",
    },
    columnPaper: {
        display: "grid",
        gridTemplateColumns: "25% 25% 25% 25%",
        padding: "5px",
        margin: "10px 5px",
    },
}));

const headers = [
    { label: "Order", key: "order" },
    { label: "Selected Cuts", key: "cuts" },
    { label: "Amount", key: "amount" },
];

let orders = [];
let all_progress_complete_data = [];
const OrderTransfer = (props) => {
    const [open, setOpen] = React.useState(false);
    const theme = useTheme();
    const fullScreen = useMediaQuery(theme.breakpoints.down("sm"));
    const classes = useStyles();
    const [selectedOptions, setSelectedOptions] = React.useState([]);
    const [selectedOrders, setSelectedOrders] = React.useState([]);
    const [finalData, setFinalData] = React.useState([]);
    const [Loading, setLoading] = React.useState(false);

    const handleDisagreeDialogClick = () => {
        setOpen(false);
    };
    const handleAgreeDialogClick = () => {
        setOpen(false);
        transfer();
    };
    React.useEffect(() => {
        NotificationManager.info("Fetching orders");
        setLoading(true);
        let userInfo = JSON.parse(localStorage.getItem("userInfo"));
        fetch(
            serverIP +
                "/TransferScannings/getOrdersFromProgressComplete.php?unit_id=" +
                userInfo.unitID
        )
            .then((response) => {
                if (response.ok) {
                    return response.json();
                } else {
                    throw new Error("Something went wrong");
                }
            })
            .then((data) => {
                setLoading(false);
                try {
                    if (data.Error_No === 0) {
                        var columnsJSON = [];
                        all_progress_complete_data = data.Orders;
                        data.Orders.map((order) => {
                            var colResult = {};
                            var repeated = orders.filter(function (a) {
                                return a.value == order.orderID;
                            }).length;
                            if (!repeated) {
                                colResult["value"] = order.orderID;
                                colResult["label"] = order.orderID;
                                colResult["complete/partial"] = "complete";
                                colResult["selected_cuts"] = [];
                                colResult["order_amount"] = 0;
                                orders.push(colResult);
                            }
                        });
                    } else {
                        NotificationManager.error(
                            "Unable to fetch Orders" + data.Error_Description
                        );
                    }
                } catch (ex) {
                    console.log("error");
                }
            })
            .catch((error) => {
                setLoading(false);
                NotificationManager.error(
                    "Failed to fetch, connection to server failed"
                );
                console.log(error);
            });
    }, []);
    const handleOrderSelectionChange = (event) => {
        if (event !== null) {
            var difference = event.filter((x) => !selectedOrders.includes(x));
            difference.map((order) => {
                order["complete/partial"] = "complete";
                order["status"] = "pending";
                order["selected_cuts"] = [];
            });
            setSelectedOrders(event);
        } else {
            setSelectedOrders([]);
        }
    };
    const OrdersList = () => {
        return (
            <List className={classes.root} subheader={<li />}>
                {selectedOrders.map((item) => (
                    <li>
                        <OrderTransferItem
                            status={item.status}
                            Order={item.value}
                            AllProgressCompleteData={all_progress_complete_data}
                            selectedOrders={selectedOrders}
                        ></OrderTransferItem>
                    </li>
                ))}
            </List>
        );
    };
    const generateExcel = () => {
        let orders_excel_data = [];
        var total_amount_for_pay_roll = 0;
        selectedOrders.map((order) => {
            let orders_data = {};
            let cuts_data = "";
            if (order["complete/partial"] === "complete") {
                let order_amount = 0;
                let cuts = "";
                let count = 0;
                all_progress_complete_data.map((item) => {
                    if (item.orderID === order.value) {
                        var colResult = {};
                        // colResult["order"] = item.orderID;
                        // colResult["cut"] = item.CutID;
                        // colResult["amount"] = item.CutwiseAmount;
                        // cuts_data.push(colResult);

                        if (count > 0) {
                            cuts = cuts + " , " + item.CutID;
                        } else {
                            cuts = item.CutID;
                        }
                        count = count + 1;
                        order_amount =
                            parseFloat(order_amount) +
                            parseFloat(item.CutwiseAmount);
                    }
                });
                orders_data["order"] = order.value;
                orders_data["cuts"] = cuts;
                orders_data["amount"] = Math.round(order_amount);
                total_amount_for_pay_roll =
                    parseFloat(total_amount_for_pay_roll) +
                    parseFloat(order_amount);
                orders_excel_data.push(orders_data);
            } else {
                let order_amount = 0;
                let cuts = "";
                let count = 0;
                order.selected_cuts.map((cut) => {
                    if (cut.selection) {
                        // var colResult = {};
                        // colResult["order"] = order.value;
                        // colResult["cut"] = cut.cut;
                        // colResult["amount"] = cut.amount;
                        // cuts_data.push(colResult);
                        if (count > 0) {
                            cuts = cuts + " , " + cut.cut;
                        } else {
                            cuts = cut.cut;
                        }
                        count = count + 1;
                        order_amount =
                            parseFloat(order_amount) + parseFloat(cut.amount);
                    }
                });
                if (count > 0) {
                    orders_data["order"] = order.value;
                    orders_data["cuts"] = cuts;
                    orders_data["amount"] = Math.round(order_amount);
                    orders_excel_data.push(orders_data);
                    total_amount_for_pay_roll =
                        parseFloat(total_amount_for_pay_roll) +
                        parseFloat(order_amount);
                } else {
                    NotificationManager.error(
                        "Select Cuts for order: " + order.value
                    );
                    return false;
                }
            }
        });
        let total_amount_info = {};
        total_amount_info["order"] = "";
        total_amount_info["cuts"] = "Total Amount";
        total_amount_info["amount"] = Math.round(total_amount_for_pay_roll);
        orders_excel_data.push(total_amount_info);
        setFinalData(orders_excel_data);
        return true;
    };
    const handleTransferClick = () => {
        setOpen(true);
    };
    const transfer = () => {
        let orders;
        let cuts;
        if (selectedOrders.length > 0) {
            // setOpen(true);
            let orders_excel_data = [];
            var total_amount_for_pay_roll = 0;
            selectedOrders.map((order) => {
                let orders_data = {};
                let cuts_data = "";
                if (order["complete/partial"] === "complete") {
                    let order_amount = 0;
                    let cuts = [];
                    let count = 0;
                    all_progress_complete_data.map((item) => {
                        if (item.orderID === order.value) {
                            var colResult = {};
                            // colResult["order"] = item.orderID;
                            // colResult["cut"] = item.CutID;
                            // colResult["amount"] = item.CutwiseAmount;
                            // cuts_data.push(colResult);

                            cuts.push(item.CutID);
                            order_amount = order_amount + item.CutwiseAmount;
                        }
                    });
                    orders_data["order"] = order.value;
                    orders_data["cuts"] = cuts;
                    orders_data["amount"] = Math.round(order_amount);
                    orders_data["selection"] = "complete";
                    total_amount_for_pay_roll =
                        total_amount_for_pay_roll + order_amount;
                    orders_excel_data.push(orders_data);
                } else {
                    let order_amount = 0;
                    let cuts = [];
                    let count = 0;
                    order.selected_cuts.map((cut) => {
                        if (cut.selection) {
                            // var colResult = {};
                            // colResult["order"] = order.value;
                            // colResult["cut"] = cut.cut;
                            // colResult["amount"] = cut.amount;
                            // cuts_data.push(colResult);
                            cuts.push(cut.cut);
                            count = count + 1;
                            order_amount = order_amount + cut.amount;
                        }
                    });
                    if (count > 0) {
                        orders_data["order"] = order.value;
                        orders_data["cuts"] = cuts;
                        orders_data["amount"] = Math.round(order_amount);
                        orders_data["selection"] = "partial";
                        orders_excel_data.push(orders_data);
                        total_amount_for_pay_roll =
                            total_amount_for_pay_roll + order_amount;
                    } else {
                        NotificationManager.error(
                            "Select Cuts for order: " + order.value
                        );
                        return false;
                    }
                }
            });
            let total_amount_info = {};
            total_amount_info["order"] = "";
            total_amount_info["cuts"] = "Total Amount";
            //   orders_excel_data.push(total_amount_info);
            setPayRollMonth(orders_excel_data);
        } else {
            NotificationManager.error("No Orders selected for transfer");
            NotificationManager.info(
                "Select Orders and their respective Cuts for transfer"
            );
        }
    };
    async function setPayRollMonth(orders_data) {
        let requests = [];
        NotificationManager.info("Transfering Scannings");
        for (let x = 0; x < orders_data.length; x++) {
            let cut_count = orders_data[x].cuts.length;
            let cut_uploaded_count = 0;
            let cuts = orders_data[x].cuts;
            for (let y = 0; y < cuts.length; y++) {
                setLoading(true);
                let userInfo = JSON.parse(localStorage.getItem("userInfo"));
                await fetch(
                    serverIP + "TransferScannings/transferPayrollCutWise.php",
                    {
                        method: "POST",
                        body: JSON.stringify({
                            orders: orders_data[x].order,
                            Cuts: cuts[y],
                            month: monthNames[new Date().getMonth()],
                            unit_id: userInfo.unitID,
                        }),
                    }
                )
                    .then((response) => {
                        if (response.ok) {
                            return response.json();
                        } else {
                            throw new Error("Something went wrong");
                        }
                    })
                    .then((data) => {
                        setLoading(false);
                        try {
                            if (data.Error_No == 0) {
                                var columnsJSON = [];
                                console.log(data);
                                cut_uploaded_count++;
                            } else {
                                NotificationManager.error(
                                    "Unable to fetch Scannings" +
                                        data.Error_Description
                                );
                            }
                        } catch (ex) {
                            console.log("error");
                        }
                    })
                    .catch((error) => {
                        console.log(error);
                        setLoading(false);
                        // var temp = [...selectedOrders];
                        // temp.map((item) => {
                        //     if (item.value === orders_data[x].order) {
                        //         item.status = "failed";
                        //     }
                        // });
                        // setSelectedOrders(temp);
                        NotificationManager.error(
                            "Failed to fetch, check your server or server file, Unexpected response from server file or the application is unable to reach server file"
                        );
                    });
            }
            if (cut_uploaded_count === cut_count) {
                var temp = [...selectedOrders];
                temp.map((item) => {
                    if (item.value === orders_data[x].order) {
                        item.status = "complete";
                    }
                });
                setSelectedOrders(temp);
            } else {
                var temp = [...selectedOrders];
                temp.map((item) => {
                    if (item.value === orders_data[x].order) {
                        item.status = "failed";
                    }
                });
                setSelectedOrders(temp);
            }
        }
    }
    const transferScanningRequest = async (order) => {
        console.log(order);
    };

    return (
        <>
            <LoadingOverlay
                active={Loading}
                spinner
                text={"Fetching orders data"}
            >
                <div
                    style={{
                        display: "flex",
                        justifyContent: "center",
                        padding: "0px 0px 15px",
                    }}
                >
                    <Typography variant="h5">
                        Order Scanning Transfer
                    </Typography>
                </div>
                <Paper className={classes.paper}>
                    <Grid container>
                        <Grid
                            item
                            lg={4}
                            md={4}
                            sm={4}
                            xs={4}
                            style={{ padding: "0px 5px" }}
                        >
                            <Select
                                styles={{ backgroundColor: "primary" }}
                                isMulti
                                label="Order"
                                onChange={handleOrderSelectionChange}
                                options={orders}
                                isSearchable={true}
                                components={animatedComponents}
                                helperText="Please select your order"
                                variant="outlined"
                            />
                        </Grid>
                        <Grid
                            item
                            lg={4}
                            md={4}
                            sm={4}
                            xs={4}
                            style={{ padding: "0px 5px" }}
                        ></Grid>
                        <Grid
                            item
                            lg={4}
                            md={4}
                            sm={4}
                            xs={4}
                            style={{
                                padding: "0px 5px",
                                display: "flex",
                                justifyContent: "flex-end",
                            }}
                        >
                            <CSVLink
                                data={finalData}
                                headers={headers}
                                asyncOnClick={true}
                                onClick={(event, done) => {
                                    if (generateExcel()) {
                                        done(); //  Your click handling logic
                                    } else {
                                        done(false);
                                    }
                                }}
                                filename={
                                    "Scannings data " +
                                    monthNames[new Date().getMonth()] +
                                    ".csv"
                                }
                                target="_blank"
                            >
                                <Fab
                                    color="primary"
                                    aria-label="Next"
                                    style={{
                                        marginRight: "20px",
                                        width: "50px",
                                        height: "50px",
                                    }}
                                >
                                    <DescriptionIcon />
                                </Fab>
                            </CSVLink>

                            <Button
                                variant="contained"
                                color="primary"
                                onClick={handleTransferClick}
                                disableElevation
                                style={{
                                    height: "50px",
                                }}
                            >
                                Transfer Scannings
                            </Button>
                        </Grid>
                        <Grid
                            item
                            lg={2}
                            md={2}
                            sm={2}
                            xs={2}
                            style={{ margin: "0px 0px 0px 0px" }}
                        ></Grid>
                    </Grid>
                </Paper>
                <Paper
                    className={classes.paper}
                    style={{
                        backgroundColor: "#900C3E",
                        color: "#fff",
                        margin: "20px 10px 10px 10px",
                    }}
                >
                    <Grid container>
                        <Grid item lg={3} md={3} sm={3} xs={3}>
                            <Typography
                                style={{ textAlign: "center" }}
                                variant="subtitle2"
                            >
                                ORDER
                            </Typography>
                        </Grid>
                        <Grid item lg={3} md={3} sm={3} xs={3}>
                            <Typography
                                style={{ textAlign: "center" }}
                                variant="subtitle2"
                            >
                                COMPLETE/PARTIAL
                            </Typography>
                        </Grid>

                        <Grid item lg={3} md={3} sm={3} xs={3}>
                            <Typography
                                style={{ textAlign: "center" }}
                                variant="subtitle2"
                            >
                                TOTAL AMOUNT OF ORDER
                            </Typography>
                        </Grid>

                        <Grid item lg={3} md={3} sm={3} xs={3}>
                            <Typography
                                style={{ textAlign: "center" }}
                                variant="subtitle2"
                            >
                                CUTS
                            </Typography>
                        </Grid>
                    </Grid>
                </Paper>
                <Dialog
                    fullScreen={fullScreen}
                    open={open}
                    aria-labelledby="responsive-dialog-title"
                >
                    <DialogTitle id="responsive-dialog-title">
                        {"Proceed with scanning transfer?"}
                    </DialogTitle>
                    <DialogContent>
                        <DialogContentText>
                            Are you sure you want to procceed with scanning
                            transfer ?.
                        </DialogContentText>
                    </DialogContent>
                    <DialogActions>
                        <Button
                            autoFocus
                            onClick={handleDisagreeDialogClick}
                            color="primary"
                        >
                            Disagree
                        </Button>
                        <Button
                            onClick={handleAgreeDialogClick}
                            color="primary"
                            autoFocus
                        >
                            Agree
                        </Button>
                    </DialogActions>
                </Dialog>
                {selectedOptions.length != null && <OrdersList />}
            </LoadingOverlay>
        </>
    );
};

export default OrderTransfer;
