import React from "react";
import { Paper, TextField, Typography, Grid } from "@material-ui/core";
import { green, pink } from "@material-ui/core/colors";
import Avatar from "@material-ui/core/Avatar";
import { NotificationManager } from "react-notifications";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import { connect, ReactReduxContext } from "react-redux";
import makeAnimated from "react-select/animated";
import Select from "react-select";
import LoadingOverlay from "react-loading-overlay";
import { serverIP } from "../../../util/Constants";
import MaterialTableDemo from "../../../components/RateAdjustment/RateAdjustmentTable";

const animatedComponents = makeAnimated();
const useStyles = makeStyles((theme) => ({
    paper: {
        padding: "20px",
    },
    field: {
        //width: '200px'
    },
    greenAvatar: {
        marginRight: "10px",
        color: "#fff",
        backgroundColor: green[500],
    },
    root: {
        width: "100%",

        backgroundColor: theme.palette.background.paper,
        position: "relative",
        overflow: "auto",
        maxHeight: "850px",
    },
    columnPaper: {
        display: "grid",
        gridTemplateColumns: "25% 25% 25% 25%",
        padding: "5px",
        margin: "10px 5px",
    },
}));
function createData(
    orderID,
    styleID,
    Major_operation_description,
    operationID,
    SMV,
    operationSequence,
    sectionDescription,
    pieceRate,
	SL
) {
    return {
        orderID,
        styleID,
        Major_operation_description,
        operationID,
        SMV,
        operationSequence,
        sectionDescription,
        pieceRate,
		SL
    };
}
let orders = [];
let selected_order = null;
let data = [];

let pieceRateSum = 0,
    SMVSum = 0;

const RateAdjustment = (props) => {
    const classes = useStyles();
    const [Loading, setLoading] = React.useState(false);
    let [rows, setRows] = React.useState([]);
    const [headCells, setHeadCells] = React.useState([
        {
            field: "orderID",
            title: "Order ID",
            editable: false,
        },
        {
            field: "operationID",
            title: "Major Operation ID",
            editable: false,
        },
        {
            field: "Major_operation_description",
            title: "Major Operation",
            editable: false,
        },
        {
            field: "SMV",
            title: "SMV",
        },
        {
            field: "operationSequence",
            title: "Operation Sequence",
            editable: false,
        },
        {
            field: "sectionDescription",
            title: "Section",
            editable: false,
        },
        {
            field: "pieceRate",
            title: "Piece Rate",
        },
        {
            field: "SL",
            title: "SL",
        }
    ]);

    React.useEffect(() => {
        setLoading(true);
        pieceRateSum = 0;
        SMVSum = 0;

        let userInfo = JSON.parse(localStorage.getItem("userInfo"));
        fetch(
            serverIP +
                "/StyleBulletin/StyleBulletinUpdate/getOrdersFromStyleBulletin.php" +
                "?unitID=" +
                userInfo.unitID
        )
            .then((response) => {
                if (response.ok) {
                    return response.json();
                } else {
                    throw new Error("Something went wrong");
                }
            })
            .then((data) => {
                setLoading(false);
                try {
                    if (data.Error_No === 0) {
                        var columnsJSON = [];
                        data.Orders.map((order) => {
                            var colResult = {};
                            var repeated = orders.filter(function (a) {
                                return a.value == order.orderID;
                            }).length;
                            if (!repeated) {
                                colResult["value"] = order.orderID;
                                colResult["label"] = order.orderID;
                                orders.push(colResult);
                            }
                        });
                    } else {
                        NotificationManager.error(
                            "Unable to fetch Orders" + data.Error_Description
                        );
                    }
                } catch (ex) {
                    console.log("error");
                }
            })
            .catch((error) => {
                setLoading(false);
                NotificationManager.error(
                    "Failed to fetch, connection to server failed"
                );
                console.log(error);
            });
    }, []);
    function update_piece_rate(styleID, pieceRate,smv, newData, oldData) {
        setLoading(true);
        fetch(
            serverIP +
                "/StyleBulletin/StyleBulletinUpdate/updatePieceRateForStyle.php?" +
                "styleID=" +
                styleID +
                "&&rate=" +
                pieceRate +
                "&&smv=" +
                smv
        )
            .then((response) => {
                if (response.ok) {
                    return response.json();
                } else {
                    throw new Error("Something went wrong");
                }
            })
            .then((data) => {
                setLoading(false);
                try {
                    if (data.Error_No === "0") {
                        var columnsJSON = [];
                        NotificationManager.success(
                            "Stylebulletin updated successfully"
                        );
                        let temp = [...rows];
                        temp[temp.indexOf(oldData)] = newData;

                        pieceRateSum = temp.reduce((sum, current) => sum += parseFloat(current.pieceRate), 0);
                        SMVSum = temp.reduce((sum, current) => sum += parseFloat(current.SMV), 0);

                        setRows(temp);
                    } else {
                        NotificationManager.error(
                            "Unable to update Stylebulletin" +
                                data.Error_Description
                        );
                    }
                } catch (ex) {
                    console.log("error");
                }
            })
            .catch((error) => {
                setLoading(false);
                NotificationManager.error(
                    "Failed to fetch, connection to server failed"
                );
                console.log(error);
            });
    }
    function delete_operation(styleID,newData, oldData) {
        setLoading(true);
        fetch(
            serverIP +
                "/StyleBulletin/StyleBulletinUpdate/deleteOperationFromStyle.php?" +
                "styleID=" +
                styleID
        )
            .then((response) => {
                if (response.ok) {
                    return response.json();
                } else {
                    throw new Error("Something went wrong");
                }
            })
            .then((data) => {
                setLoading(false);
                try {
                    if (data.Error_No === "0") {
                        var columnsJSON = [];
                        NotificationManager.success(
                            "Operation deleted successfully"
                        );
                    let temp = [...rows];
                     temp = temp.filter(function (el) {
                        return el.styleID != styleID;
                      });
                    setRows(temp);
                    setLoading(false);
                    } else {
                        NotificationManager.error(
                            "Unable to delete operation from Stylebulletin" +
                                data.Error_Description
                        );
                    }
                } catch (ex) {
                    console.log("error");
                }
            })
            .catch((error) => {
                setLoading(false);
                NotificationManager.error(
                    "Failed to fetch, connection to server failed"
                );
                console.log(error);
            });
    }
    function get_style_bulletin_for_order(order) {
        let style_bulletin = [];
        setLoading(true);
        fetch(
            serverIP +
                "/StyleBulletin/StyleBulletinUpdate/getStyleBulletinForOrder.php?" +
                "orderID=" +
                order
        )
            .then((response) => {
                if (response.ok) {
                    return response.json();
                } else {
                    throw new Error("Something went wrong");
                }
            })
            .then((data) => {
                setLoading(false);
                try {
                    if (data.Error_No === 0) {
                        var columnsJSON = [];
                        data.Style_bulletin.map((style) => {
                            style_bulletin.push(
                                createData(
                                    style.orderID,
                                    style.styleID,
                                    style.Major_operation_description,
                                    style.Operation_code,
                                    style.SMV,
                                    style.operationSequence,
                                    style.sectionDescription,
                                    style.pieceRate,
									style.SL
                                )
                            );
                        });

                        pieceRateSum = style_bulletin.reduce((sum, current) => sum += parseFloat(current.pieceRate), 0);
                        SMVSum = style_bulletin.reduce((sum, current) => sum += parseFloat(current.SMV), 0);

                        setRows(style_bulletin);
                    } else {
                        NotificationManager.error(
                            "Unable to fetch Style Bulletin" +
                                data.Error_Description
                        );
                    }
                } catch (ex) {
                    console.log("error");
                }
            })
            .catch((error) => {
                setLoading(false);
                NotificationManager.error(
                    "Failed to fetch, connection to server failed"
                );
                console.log(error);
            });
    }
    const handleOrderDropDownChange = (event) => {
        setRows([]);
        SMVSum = 0;
        pieceRateSum = 0;

        if (event != null) {
            selected_order = event.value;
            get_style_bulletin_for_order(selected_order);
        } else {
            selected_order = null;
        }
    };

    return (
        <>
            <LoadingOverlay active={Loading} spinner text={"Fetching data"}>
                <div
                    style={{
                        display: "flex",
                        justifyContent: "center",
                        padding: "0px 0px 15px",
                    }}
                >
                    <Typography variant="h5">View Style Bulletin</Typography>
                </div>
                <Paper className={classes.paper}>
                    <Grid container>
                        <Grid item lg={1} md={1} sm={1} xs={1}>
                            <Typography
                                style={{
                                    textAlign: "center",
                                    marginTop: "10px",
                                }}
                                variant="subtitle2"
                            >
                                ORDER
                            </Typography>
                        </Grid>
                        <Grid item lg={2} md={2} sm={2} xs={2}>
                            <Select
                                className="basic-single"
                                label="Cut"
                                onChange={handleOrderDropDownChange}
                                options={orders}
                                isSearchable={true}
                                components={animatedComponents}
                                helperText="Please select your order"
                                variant="outlined"
                            />
                        </Grid>
                        {pieceRateSum !== 0 &&
                            <Grid
                                item xs={2}
                                style={{marginLeft: 'auto'}}
                            >
                                <Typography
                                    style={{
                                        textAlign: "center",
                                        marginTop: "10px",
                                    }}
                                    variant="subtitle2"
                                >
                                    Total Piece Rate: {pieceRateSum.toPrecision(4)}
                                </Typography>
                            </Grid>
                        }
                        {SMVSum !== 0 &&
                            <Grid item xs={2}>
                                <Typography
                                    style={{
                                        textAlign: "center",
                                        marginTop: "10px",
                                    }}
                                    variant="subtitle2"
                                >
                                    Total SMV: {SMVSum.toPrecision(4)}
                                </Typography>
                            </Grid>
                        }

                        {/* <Grid
                            item
                            lg={5}
                            md={5}
                            sm={5}
                            xs={5}
                            style={{
                                padding: "0px 5px",
                                display: "flex",
                                justifyContent: "flex-end",
                            }}
                        >
                            <Button
                                variant="contained"
                                color="primary"
                                disableElevation
                                style={{
                                    height: "50px",
                                }}
                            >
                                Transfer Scannings
                            </Button>
                        </Grid> */}
                    </Grid>
                </Paper>
                <Paper
                    className={classes.paper}
                    style={{
                        backgroundColor: "#900C3E",
                        color: "#fff",
                        margin: "10px 10px 10px 10px",
                    }}
                >
                    <Grid container>
                        <Grid item lg={12} md={12} sm={12} xs={12}>
                            {rows.length > 0 && (
                                <MaterialTableDemo
                                    style={{
                                        padding: "15px",
                                    }}
                                    headCells={headCells}
                                    Rows={rows}
                                    UpdatePieceRate={update_piece_rate}
                                    DeleteOperation={delete_operation}
                                />
                            )}
                        </Grid>
                    </Grid>
                </Paper>
            </LoadingOverlay>
        </>
    );
};

export default RateAdjustment;
