import React from 'react';
import { TextField, Button, Grid, Typography, Paper } from '@material-ui/core';
import Autocomplete from '@material-ui/lab/Autocomplete';
import { makeStyles } from '@material-ui/core/styles';
import { NotificationManager } from "react-notifications";
import { withSnackbar } from 'notistack';
import LoadingOverlay from "react-loading-overlay";
import {
    serverIP,
    getAllSections,
	getLinesForUnit,
	addUser
} from "../../util/Constants";


const useStyles = makeStyles(theme => ({
	field: {
		//width:'250px'
	},
	grid: {
		padding: '10px'
	},
	paper: {
		padding: "20px",
	},
}));

let usertypes = [{ title: "WEB PORTAL", value: "WEB_PORTAL" }, { title: "AUDITOR", value: "AUDITOR" }];

const Add = props => {
	const [Loading, setLoading] = React.useState(false);
	const classes = useStyles();
	const [state, setState] = React.useState({ name: '', username: '', password: '', userType: '', line:'',section:'' });
	const [lines, setLines] = React.useState([]);
	const [sections, setSections] = React.useState([]);

	React.useEffect(() => {
		get_all_lines();
		get_all_sections();
	}, []);
	const handleChange = e => {
		setState({ ...state, [e.target.name]: e.target.value });
	};
	const  handleSubmit = async () => {
		setLoading(true);
		console.log(state);
		let userInfo = JSON.parse(localStorage.getItem("userInfo"));
		await fetch(serverIP + addUser, {
			method: "POST",
			body: JSON.stringify({
				username: state.username,
				password: state.password,
				section_id: state.section.section_id,
				section_code: state.section.section_code,
				section_description: state.section.section_description,
				line_id: state.line.line_id,
				line_code: state.line.line_code,
				line_description: state.line.line_code,
				unit_id: userInfo.unitID,
				user_type: state.userType,
			}),
		})
			.then((response) => {
				if (response.ok) {
					return response.json();
				} else {
					throw new Error("Something went wrong");
				}
			})
			.then((data) => {
				setLoading(false);
				try {
					if (data.Error_No === "0") {
						NotificationManager.success(
							"User has been added"
						);

					} else {
						NotificationManager.error(
							"Unable to add user" +
							data.Error_Description
						);
					}
				} catch (ex) {
					console.log("error");
				}
			})
			.catch((error) => {
				console.log(error);
				setLoading(false);
				NotificationManager.error(
					"Failed to fetch, check your server or server file, Unexpected response from server file or the application is unable to reach server file"
				);
			});

	};
	function get_all_sections() {
        setLoading(true);
        fetch(serverIP + getAllSections)
            .then((response) => {
                if (response.ok) {
                    return response.json();
                } else {
                    throw new Error("Something went wrong");
                }
            })
            .then((data) => {
                setLoading(false);
                try {
                    if (data.Error_No === 0) {
                        var columnsJSON = [];
                        if (data.SECTIONS) {
                            setSections(data.SECTIONS);
                        }
                    } else {
                        NotificationManager.error(
                            "Unable to fetch Sections" + data.Error_Description
                        );
                    }
                } catch (ex) {
                    console.log("error");
                }
            })
            .catch((error) => {
                setLoading(false);
                NotificationManager.error(
                    "Failed to fetch, connection to server failed"
                );
                console.log(error);
            });
    }
	function get_all_lines() {
        setLoading(true);
		let userInfo = JSON.parse(localStorage.getItem("userInfo"));
        fetch(serverIP + getLinesForUnit+"?unitID="+userInfo.unitID)
            .then((response) => {
                if (response.ok) {
                    return response.json();
                } else {
                    throw new Error("Something went wrong");
                }
            })
            .then((data) => {
                setLoading(false);
                try {
                    if (data.Error_No === 0) {
                        var columnsJSON = [];
                        if (data.LINES) {
                            setLines(data.LINES);
                        }
                    } else {
                        NotificationManager.error(
                            "Unable to fetch Lines" + data.Error_Description
                        );
                    }
                } catch (ex) {
                    console.log("error");
                }
            })
            .catch((error) => {
                setLoading(false);
                NotificationManager.error(
                    "Failed to fetch, connection to server failed"
                );
                console.log(error);
            });
    }

	return (
		<LoadingOverlay active={Loading} spinner text={"Fetching data"}>
			<div>
				<div
					style={{
						display: "flex",
						justifyContent: "center",
						padding: "0px 0px 15px",
					}}
				>
					<Typography variant="h5">Create Users</Typography>
				</div>
				<Paper className={classes.paper}>
					<Grid container>
						<Grid item lg={6} md={6} sm={6} xs={12} className={classes.grid}>
							<Autocomplete
								options={usertypes}
								getOptionLabel={(option) => option.title}
								onChange={(e,option) => {
											if(option){
												setState({ ...state, userType: option.title});
											}else{
												setState({ ...state, userType:null});
											}

										}}
								renderInput={(params) =>
									<TextField
										value={state.userType}
									 {...params} label="User Type" variant="outlined" fullWidth />}
							/>
						</Grid>
						<Grid item lg={6} md={6} sm={6} xs={12} className={classes.grid}>
							<TextField
								variant="outlined"
								label="Username"
								fullWidth
								name="username"
								className={classes.field}
								value={state.username}
								onChange={handleChange}
							//type="number"
							/>
						</Grid>
						<Grid item lg={6} md={6} sm={6} xs={12} className={classes.grid}>
							<TextField
								variant="outlined"
								label="Password"
								fullWidth
								name="password"
								className={classes.field}
								type="password"
								value={state.password}
								onChange={handleChange}
							//type="number"
							/>
						</Grid>
						<Grid item lg={6} md={6} sm={6} xs={12} className={classes.grid}>
							<Autocomplete
								options={lines}
								getOptionLabel={(option) => option.line_code}
								onChange={(e, option) => {
											console.log(option)
											if (option) {
												setState({ ...state, line: option});
											} else {
												setState({ ...state, line:null });
											}
										}}
								renderInput={(params) =>
									<TextField
										value={state.line}
										 {...params} label="Line" variant="outlined" fullWidth />}
							/>
						</Grid>
						<Grid item lg={6} md={6} sm={6} xs={12} className={classes.grid}>
							<Autocomplete
								options={sections}
								getOptionLabel={(option) => option.section_description}
								onChange={(e, option) => {
											console.log(option)
											if (option) {
												setState({ ...state, section: option});
											} else {
												setState({ ...state, section:null });
											}
										}}
								renderInput={(params) =>
									<TextField
										value={state.section}
										 {...params} label="Section" variant="outlined" fullWidth />}
							/>
						</Grid>
						<Grid item lg={12} md={12} sm={12} xs={12} className={classes.grid}>
							<Button variant="contained" fullWidth style={{ backgroundColor: '#900C3E', color: '#fff', height: '50px' }} onClick={handleSubmit}>Add User</Button>
						</Grid>
					</Grid>
				</Paper>
			</div>
		</LoadingOverlay>
	);

};

export default Add;