import React from "react";
import { Paper, TextField, Typography, Grid } from "@material-ui/core";
import { green, pink } from "@material-ui/core/colors";
import { NotificationManager } from "react-notifications";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import makeAnimated from "react-select/animated";
import Select from "react-select";
import OrderTransferItem from "../../components/orderTransfer/orderTransferListItem/orderTransferItem";
import List from "@material-ui/core/List";
import Button from "@material-ui/core/Button";
import LoadingOverlay from "react-loading-overlay";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import useMediaQuery from "@material-ui/core/useMediaQuery";
import { serverIP, monthNames } from "../../util/Constants";
import MaterialTableDemo from "../../components/DeleteScannings/DeleteScanningsTable";
import DataTable from "./datatable";

const animatedComponents = makeAnimated();
const useStyles = makeStyles((theme) => ({
    paper: {
        padding: "20px",
    },
    field: {
        //width: '200px'
    },
    greenAvatar: {
        marginRight: "10px",
        color: "#fff",
        backgroundColor: green[500],
    },
    root: {
        width: "100%",

        backgroundColor: theme.palette.background.paper,
        position: "relative",
        overflow: "auto",
        maxHeight: "850px",
    },
    columnPaper: {
        display: "grid",
        gridTemplateColumns: "25% 25% 25% 25%",
        padding: "5px",
        margin: "10px 5px",
    },
}));
function createData(
	bundle_scan_id,
    Scanning_time,
    operationAutoID,
    operationDescription,
    operationID,
    workerID,
    workerName,
    machineID,
    machineType,
    styleID,
    lineID,
    lineDescription,
    sectionID,
    sectionDescription,
    cutID,
    bundleID
) {
    return {
		bundle_scan_id,
        Scanning_time,
        operationAutoID,
        operationDescription,
        operationID,
        workerID,
        workerName,
        machineID,
        machineType,
        styleID,
        lineID,
        lineDescription,
        sectionID,
        sectionDescription,
        cutID,
        bundleID,
    };
}
let orders = [];
let operations = [];
let bundles = [];
let scannings_data = [];
let selected_operations = [];
let selected_order = null;
let selected_bundle = null;
let data = [];
const DeleteScannings = (props) => {
    let userInfo = JSON.parse(localStorage.getItem("userInfo"));
    const classes = useStyles();
    const theme = useTheme();
    const [open, setOpen] = React.useState(false);
    const [Loading, setLoading] = React.useState(false);
    const [selectedRowsForDeletion, setSelectedRowsForDeletion] = React.useState([]);
    let [rows, setRows] = React.useState([]);
    const fullScreen = useMediaQuery(theme.breakpoints.down("sm"));
    const [headCells, setHeadCells] = React.useState([
        {
            field: "Scanning_time",
            title: "Scan Time",
            disablePadding: false,
        },
        {
            field: "lineDescription",
            title: "Line Description",
            disablePadding: false,
        },
        {
            field: "operationDescription",
            title: "Operation Description",
            disablePadding: false,
        },
        {
            field: "sectionDescription",
            title: "Section Description",
            disablePadding: false,
        },
        {
            field: "workerName",
            title: "Worker Name",
            disablePadding: false,
        },
        {
            field: "cutID",
            title: "Cut",
            disablePadding: false,
        },
        {
            field: "bundleID",
            title: "Bundle",
            disablePadding: false,
        },
    ]);
    React.useEffect(() => {
        setLoading(true);
        fetch(serverIP + "/DeleteScannings/getOrdersFromProgressComplete.php"+ "?unitID=" + userInfo.unitID)
            .then((response) => {
                if (response.ok) {
                    return response.json();
                } else {
                    throw new Error("Something went wrong");
                }
            })
            .then((data) => {
                setLoading(false);
                try {
                    if (data.Error_No === 0) {
                        data.Orders.map((order) => {
                            var colResult = {};
                            var repeated = orders.filter(function (a) {
                                return a.value == order.orderID;
                            }).length;
                            if (!repeated) {
                                colResult["value"] = order.orderID;
                                colResult["label"] = order.orderID;
                                orders.push(colResult);
                            }
                        });
                    } else {
                        NotificationManager.error(
                            "Unable to fetch Orders" + data.Error_Description
                        );
                    }
                } catch (ex) {
                    console.log("error");
                }
            })
            .catch((error) => {
                setLoading(false);
                NotificationManager.error(
                    "Failed to fetch, connection to server failed"
                );
                console.log(error);
            });
    }, []);
    const handleDisagreeDialogClick = () => {
        setOpen(false);
    };
    const handleAgreeDialogClick = () => {
        setOpen(false);
        if (selected_order != null && selected_operations != null) {
            deleteScanningRequest(
                selected_order,
                selected_operations,
                monthNames[new Date().getMonth()]
            );
        } else {
            NotificationManager.error("Select order and operations");
        }
    };
    function get_operations_for_orders(order) {
        operations = [];
        setLoading(true);
        fetch(
            serverIP +
                "/DeleteScannings/getCutsForOrderFromProgressComplete.php?" +
                "orderID=" +
                order+ "&&unitID=" + userInfo.unitID
        )
            .then((response) => {
                if (response.ok) {
                    return response.json();
                } else {
                    throw new Error("Something went wrong");
                }
            })
            .then((data) => {
                setLoading(false);
                try {
                    if (data.Error_No === 0) {
                        var columnsJSON = [];
                        data.Operations.map((operation) => {
                            var colResult = {};
                            colResult["value"] = operation.operationID;
                            colResult["label"] = operation.operationID;
                            operations.push(colResult);
                        });
                    } else {
                        NotificationManager.error(
                            "Unable to fetch Operations" +
                                data.Error_Description
                        );
                    }
                } catch (ex) {
                    console.log("error");
                }
            })
            .catch((error) => {
                setLoading(false);
                NotificationManager.error(
                    "Failed to fetch, connection to server failed"
                );
                console.log(error);
            });
    }
    function get_bundles_for_cut_order(order, cut) {
        bundles = [];
        setLoading(true);
        fetch(
            serverIP +
                "/DeleteScannings/getBundlesForOrder,CutFromProgressComplete.php?" +
                "orderID=" +
                order +
                "&&cutID=" +
                cut
        )
            .then((response) => {
                if (response.ok) {
                    return response.json();
                } else {
                    throw new Error("Something went wrong");
                }
            })
            .then((data) => {
                setLoading(false);
                try {
                    if (data.Error_No === 0) {
                        var columnsJSON = [];
                        data.Bundles.map((bundle) => {
                            var colResult = {};
                            colResult["value"] = bundle.itemID;
                            colResult["label"] = bundle.bundleID;
                            bundles.push(colResult);
                        });
                    } else {
                        NotificationManager.error(
                            "Unable to fetch Bundles" + data.Error_Description
                        );
                    }
                } catch (ex) {
                    console.log("error");
                }
            })
            .catch((error) => {
                setLoading(false);
                NotificationManager.error(
                    "Failed to fetch, connection to server failed"
                );
                console.log(error);
            });
    }
    function get_scannings_for_Operation(order, operation) {
        scannings_data = [];
        setLoading(true);
        fetch(
            serverIP +
                "/DeleteScannings/getBundleScannings.php?" +
                "orderID=" +
                order +
                "&&operationAutoID=" +
                operation +
               "&&unitID=" + userInfo.unitID
        )
            .then((response) => {
                if (response.ok) {
                    return response.json();
                } else {
                    throw new Error("Something went wrong");
                }
            })
            .then((data) => {
                setLoading(false);
                try {
                    if (data.Error_No === 0) {
                        var columnsJSON = [];
                        data.Scannings.map((scan) => {
                            scannings_data.push(
                                createData(
									scan.Bundle_scan_id,
                                    scan.Scanning_time,
                                    scan.operationAutoID,
                                    scan.operationDescription,
                                    scan.operationID,
                                    scan.workerID,
                                    scan.workerName,
                                    scan.machineID,
                                    scan.machineType,
                                    scan.styleID,
                                    scan.lineID,
                                    scan.lineDescription,
                                    scan.sectionID,
                                    scan.sectionDescription,
                                    scan.cutID,
                                    scan.bundleID
                                )
                            );
                        });
                        setRows(scannings_data);
                    } else {
                        NotificationManager.error(
                            "Unable to fetch scannings " +
                                data.Error_Description
                        );
                    }
                } catch (ex) {
                    console.log("error");
                }
            })
            .catch((error) => {
                setLoading(false);
                NotificationManager.error(
                    "Failed to fetch, connection to server failed"
                );
                console.log(error);
            });
    }
    const handleOrderDropDownChange = (event) => {
        setRows([]);
        if (event != null) {
            selected_order = event.value;
            get_operations_for_orders(event.value);
        } else {
            selected_order = null;
        }
    };
    const handleOperationDropDownChange = (event) => {
        setRows([]);
        if(event){
            if (event.length > 0) {
                let selected_count = 0;
                selected_operations = "";
                event.map((operation) => {
                    if (selected_count === 0) {
                        selected_operations = operation.value;
                    } else {
                        selected_operations =
                            selected_operations + "," + operation.value;
                    }
                    selected_count++;
                });
                get_scannings_for_Operation(selected_order, selected_operations);
            } else {
                setRows([]);
                selected_operations = null;
            }
        }

    };
    const handleBundleDropDownChange = (event) => {
        setRows([]);
        if (event != null) {
            selected_bundle = event.value;
        } else {
        }
    };
    const handleDeleteScanningsButtonClick = () => {
        setOpen(true);
    };
    const deleteScanningRequest = async (order, operation, month) => {
        setLoading(true);
        await fetch(
            serverIP +
                "DeleteScannings/deleteScannings.php?scan_ids=" +
                "("+selectedRowsForDeletion.toString()+")",
            {
                method: "POST",
            }
        )
            .then((response) => {
                if (response.ok) {
                    return response.json();
                } else {
                    throw new Error("Something went wrong");
                }
            })
            .then((data) => {
                setLoading(false);
                try {
                    if (data.Error_No == "0") {
                        var columnsJSON = [];
                        setRows([]);
                        NotificationManager.success(
                            "Scannings deleted successfully"
                        );
                    } else {
                        NotificationManager.error(
                            "Unable to delete Scannings" +
                                data.Error_Description
                        );
                    }
                } catch (ex) {
                    console.log("error");
                }
            })
            .catch((error) => {
                console.log(error);
                setLoading(false);
                NotificationManager.error(
                    "Failed to fetch, check your server or server file, Unexpected response from server file or the application is unable to reach server file"
                );
            });
    };
    return (
        <>
            <LoadingOverlay active={Loading} spinner text={"Fetching data"}>
                <div
                    style={{
                        display: "flex",
                        justifyContent: "center",
                        padding: "0px 0px 15px",
                    }}
                >
                    <Typography variant="h5">Bundle Scannings</Typography>
                </div>
                <Paper className={classes.paper}>
                    <Grid container>
                        <Grid item lg={1} md={1} sm={1} xs={1}>
                            <Typography
                                style={{
                                    textAlign: "center",
                                    marginTop: "10px",
                                }}
                                variant="subtitle2"
                            >
                                ORDER
                            </Typography>
                        </Grid>
                        <Grid item lg={2} md={2} sm={2} xs={2}>
                            <Select
                                className="basic-single"
                                label="Cut"
                                onChange={handleOrderDropDownChange}
                                options={orders}
                                isSearchable={true}
                                components={animatedComponents}
                                helperText="Please select your order"
                                variant="outlined"
                            />
                        </Grid>
                        <Grid item lg={1} md={1} sm={1} xs={1}>
                            <Typography
                                style={{
                                    textAlign: "center",
                                    marginTop: "10px",
                                }}
                                variant="subtitle2"
                            >
                                OperationID
                            </Typography>
                        </Grid>
                        <Grid item lg={4} md={4} sm={4} xs={4}>
                            <Select
                                isMulti
                                label="Cut"
                                onChange={handleOperationDropDownChange}
                                options={operations}
                                isSearchable={true}
                                isClearable={true}
                                components={animatedComponents}
                                helperText="Please select your Cut"
                                variant="outlined"
                            />
                        </Grid>
                        {/* <Grid item lg={1} md={1} sm={1} xs={1}>
                            <Typography
                                style={{
                                    textAlign: "center",
                                    marginTop: "10px",
                                }}
                                variant="subtitle2"
                            >
                                BUNDLE
                            </Typography>
                        </Grid>
                        <Grid item lg={2} md={2} sm={2} xs={2}>
                            <Select
                                className="basic-single"
                                label="Bundle"
                                options={bundles}
                                isSearchable={true}
                                onChange={handleBundleDropDownChange}
                                isClearable={true}
                                components={animatedComponents}
                                helperText="Please select your bundle"
                                variant="outlined"
                            />
                        </Grid> */}
                        <Grid
                            item
                            lg={4}
                            md={4}
                            sm={4}
                            xs={4}
                            style={{
                                padding: "0px 5px",
                                display: "flex",
                                justifyContent: "flex-end",
                            }}
                        >
                            {/* <CSVLink
                                data={finalData}
                                headers={headers}
                                asyncOnClick={true}
                                onClick={(event, done) => {
                                    // if (generateExcel()) {
                                    //     done(); //  Your click handling logic
                                    // } else {
                                    //     done(false);
                                    // }
                                }}
                                filename={
                                    "Scannings data " +
                                    monthNames[new Date().getMonth()] +
                                    ".csv"
                                }
                                target="_blank"
                            > */}
                            {/* <Fab
                                    color="primary"
                                    aria-label="Next"
                                    style={{
                                        marginRight: "20px",
                                        width: "50px",
                                        height: "50px",
                                    }}
                                >
                                    <DescriptionIcon />
                                </Fab>
                            </CSVLink> */}
{/*
                            <Button
                                variant="contained"
                                color="primary"
                                disableElevation
                                onClick={handleDeleteScanningsButtonClick}
                                style={{
                                    height: "50px",
                                }}
                            >
                                Delete Scannings
                            </Button> */}
                        </Grid>
                    </Grid>
                </Paper>
                <Dialog
                    fullScreen={fullScreen}
                    open={open}
                    aria-labelledby="responsive-dialog-title"
                >
                    <DialogTitle id="responsive-dialog-title">
                        {"Proceed with scanning transfer?"}
                    </DialogTitle>
                    <DialogContent>
                        <DialogContentText>
                            Are you sure you want to procceed with delete
                            scannings ?.
                        </DialogContentText>
                    </DialogContent>
                    <DialogActions>
                        <Button
                            autoFocus
                            onClick={handleDisagreeDialogClick}
                            color="primary"
                        >
                            Disagree
                        </Button>
                        <Button
                            onClick={handleAgreeDialogClick}
                            color="primary"
                            autoFocus
                        >
                            Agree
                        </Button>
                    </DialogActions>
                </Dialog>
                <Paper
                    className={classes.paper}
                    style={{
                        backgroundColor: "#900C3E",
                        color: "#fff",
                        margin: "10px 10px 10px 10px",
                    }}
                >
                    <Grid container>
                        <Grid item lg={12} md={12} sm={12} xs={12}>
                            {rows.length > 0 && (
                                <DataTable
                                    headCells={headCells}
                                    Rows={rows}
									selectedRowsForDeletion={selectedRowsForDeletion}
									setSelectedRowsForDeletion={setSelectedRowsForDeletion}
									handleDeleteScanningsButtonClick={handleDeleteScanningsButtonClick}
                                />
                            )}
                        </Grid>
                    </Grid>
                </Paper>
            </LoadingOverlay>
        </>
    );
};

export default DeleteScannings;
