import React from "react";
import MaterialTable from "material-table";

export default function MaterialTableDemo(props) {
    const [state, setState] = React.useState({
        columns: props.headCells,
    });
    const [data, setData] = React.useState([]);

    React.useEffect(() => {
        setData(props.Rows);
    }, [props.Rows]);

    React.useEffect(() => {}, []);
    return (
        <MaterialTable
            title={"Style Bulletin"}
            columns={state.columns}
            data={data}
            editable={{
                onRowUpdate: (newData, oldData) =>
                    new Promise((resolve) => {
                        setTimeout(() => {
                            resolve();
                            props.UpdatePieceRate(
                                newData.styleID,
                                newData.pieceRate,
                                newData.SMV,
                                newData,
                                oldData
                            );
                        }, 600);
                    }),

                    onRowDelete: (newData, oldData) =>
                    new Promise((resolve) => {
                        setTimeout(() => {
                            resolve();
                            props.DeleteOperation(
                                newData.styleID,
                                newData,
                                oldData
                            );
                        }, 600);
                    }),
            }}
        />
    );
}
