import MUIDataTable from "mui-datatables";
import React from "react";
const Datatable = (props) => {
	const columns = [
		{
			name: "Scanning_time",
			label: "Scan Time",
			options: {
				filter: false,
				sort: true,
			}
		},
		{
			name: "lineDescription",
			label: "Line Description",
			options: {
				filter: true,
				sort: false,
			}
		},
		{
			name: "operationDescription",
			label: "Operation Description",
			options: {
				filter: true,
				sort: false,
			}
		},
		{
			name: "sectionDescription",
			label: "Section Description",
			options: {
				filter: true,
				sort: false,
			}
		},
		{
			name: "workerName",
			label: "Worker Name",
			options: {
				filter: true,
				sort: false,
			}
		},
		{
			name: "cutID",
			label: "Cut",
			options: {
				filter: true,
				sort: false,
			}
		},
		{
			name: "bundleID",
			label: "Bundle",
			options: {
				filter: true,
				sort: false,
			}
		},
	];
	const data = props.Rows;
	const options = {
		filterType: 'dropdown',
		onRowsDelete: (rowsDeleted) => {
			let rowsToBeDeleted= [];
			rowsDeleted.data.map((index)=>{
				rowsToBeDeleted.push(data[index.dataIndex].bundle_scan_id);
			})
			props.setSelectedRowsForDeletion(rowsToBeDeleted)
			props.handleDeleteScanningsButtonClick()
			return false;
		},
		toolbar: {
			search: "Search",
			downloadCsv: false,
			print: false,
			viewColumns: "View Columns",
			filterTable: "Filter Table",
		  },

	};
	return (
		<MUIDataTable
			title={"Bundle scannings"}
			data={data}
			columns={columns}
			options={options}
		/>
	);
};

export default(Datatable)