import React from "react";
import { Paper, TextField, Typography, Grid } from "@material-ui/core";
import { green, pink } from "@material-ui/core/colors";
import Avatar from "@material-ui/core/Avatar";
import { NotificationManager } from "react-notifications";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import makeAnimated from "react-select/animated";
import LoadingOverlay from "react-loading-overlay";
import {
    serverIP,
    getAllWorkers,
    updateWorkerInfo,
} from "../../util/Constants";
import MaterialTableDemo from "../../components/UpdateBundleQuantity/BundleQuantityTable";

const animatedComponents = makeAnimated();
const useStyles = makeStyles((theme) => ({
    paper: {
        padding: "20px",
    },
    field: {
        //width: '200px'
    },
    greenAvatar: {
        marginRight: "10px",
        color: "#fff",
        backgroundColor: green[500],
    },
    root: {
        width: "100%",

        backgroundColor: theme.palette.background.paper,
        position: "relative",
        overflow: "auto",
        maxHeight: "850px",
    },
    columnPaper: {
        display: "grid",
        gridTemplateColumns: "25% 25% 25% 25%",
        padding: "5px",
        margin: "10px 5px",
    },
}));
function createData(workerID, workerName, empTypeCode, status) {
    return { workerID, workerName, empTypeCode, status };
}
let workers = [];
const Operation = (props) => {
    const classes = useStyles();
    const [Loading, setLoading] = React.useState(false);
    let [rows, setRows] = React.useState([]);
    const [headCells, setHeadCells] = React.useState([
        {
            field: "workerID",
            title: "Worker ID",
            editable: false,
        },
        {
            field: "workerName",
            title: "Worker Name",
        },
        {
            field: "empTypeCode",
            title: "EMP Type Code",
        },
        {
            field: "status",
            title: "Status",
        },
    ]);
    React.useEffect(() => {
        get_all_workers();
    }, []);

    function get_all_workers() {
        setRows([]);
        setLoading(true);
        let userInfo = JSON.parse(localStorage.getItem("userInfo"));
        fetch(serverIP + getAllWorkers + "?unitID=" + userInfo.unitID)
            .then((response) => {
                if (response.ok) {
                    return response.json();
                } else {
                    throw new Error("Something went wrong");
                }
            })
            .then((data) => {
                setLoading(false);
                try {
                    if (data.Error_No === 0) {
                        var columnsJSON = [];
                        data.Workers.map((worker) => {
                            workers.push(
                                createData(
                                    worker.workerID,
                                    worker.workerName,
                                    worker.empTypeCode,
                                    worker.status
                                )
                            );
                        });
                        setRows(workers);
                    } else {
                        NotificationManager.error(
                            "Unable to fetch Workers " + data.Error_Description
                        );
                    }
                } catch (ex) {
                    console.log("error");
                }
            })
            .catch((error) => {
                setLoading(false);
                NotificationManager.error(
                    "Failed to fetch, connection to server failed"
                );
                console.log(error);
            });
    }
    function update_bundle_quantity(itemID, quantity, newData, oldData) {
        setLoading(true);
        fetch(
            serverIP +
                updateWorkerInfo +
                "?" +
                "workerID=" +
                oldData.workerID +
                "&&workerName=" +
                newData.workerName
        )
            .then((response) => {
                if (response.ok) {
                    return response.json();
                } else {
                    throw new Error("Something went wrong");
                }
            })
            .then((data) => {
                setLoading(false);
                try {
                    if (data.Error_No === "0") {
                        var columnsJSON = [];
                        NotificationManager.success(
                            "Worker updated successfully"
                        );
                        let temp = [...rows];
                        temp[temp.indexOf(oldData)] = newData;
                        setRows(temp);
                    } else {
                        NotificationManager.error(
                            "Unable to update Worker" + data.Error_Description
                        );
                    }
                } catch (ex) {
                    console.log("error");
                }
            })
            .catch((error) => {
                setLoading(false);
                NotificationManager.error(
                    "Failed to fetch, connection to server failed"
                );
                console.log(error);
            });
    }

    return (
        <>
            <LoadingOverlay active={Loading} spinner text={"Fetching data"}>
                <div
                    style={{
                        display: "flex",
                        justifyContent: "center",
                        padding: "0px 0px 15px",
                    }}
                >
                    <Typography variant="h5">Workers</Typography>
                </div>

                <Paper
                    className={classes.paper}
                    style={{
                        backgroundColor: "#900C3E",
                        color: "#fff",
                        margin: "10px 10px 10px 10px",
                    }}
                >
                    <Grid container>
                        <Grid item lg={12} md={12} sm={12} xs={12}>
                            {rows.length > 0 && (
                                <MaterialTableDemo
                                    headCells={headCells}
                                    Rows={rows}
                                    UpdateQuantityFunction={
                                        update_bundle_quantity
                                    }
                                />
                            )}
                        </Grid>
                    </Grid>
                </Paper>
            </LoadingOverlay>
        </>
    );
};

export default Operation;
