import React from "react";
import { Paper, TextField, Typography, Grid } from "@material-ui/core";
import { green, pink } from "@material-ui/core/colors";
import Avatar from "@material-ui/core/Avatar";
import { NotificationManager } from "react-notifications";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import { connect, ReactReduxContext } from "react-redux";
import makeAnimated from "react-select/animated";
import Select from "react-select";
import OrderTransferItem from "../../../components/orderTransfer/orderTransferListItem/orderTransferItem";
import List from "@material-ui/core/List";
import Button from "@material-ui/core/Button";
import LoadingOverlay from "react-loading-overlay";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import useMediaQuery from "@material-ui/core/useMediaQuery";
import DescriptionIcon from "@material-ui/icons/Description";
import Fab from "@material-ui/core/Fab";
import {
	serverIP,
	forViewgetOrdersFromCutReport,
	forViewgetCutsForOrder,
	forViewgetBundlesForOrderCut,
	updateBundle,
} from "../../../util/Constants";
import MaterialTableDemo from "../../../components/UpdateBundleQuantity/BundleQuantityTable";

const animatedComponents = makeAnimated();
const useStyles = makeStyles((theme) => ({
	paper: {
		padding: "20px",
	},
	field: {
		//width: '200px'
	},
	greenAvatar: {
		marginRight: "10px",
		color: "#fff",
		backgroundColor: green[500],
	},
	root: {
		width: "100%",

		backgroundColor: theme.palette.background.paper,
		position: "relative",
		overflow: "auto",
		maxHeight: "850px",
	},
	columnPaper: {
		display: "grid",
		gridTemplateColumns: "25% 25% 25% 25%",
		padding: "5px",
		margin: "10px 5px",
	},
}));
function createData(itemID, bundle, quantity, size,special_process) {
	return { itemID, bundle, quantity, size ,special_process};
}
let orders = [];
let cuts = [];
let all_progress_complete_data = [];
var orders_excel_data = [];
let selected_cut = null;
let selected_order = null;
let data = [];

let quantitySum = 0;

const UpdateQuantity = (props) => {
	const classes = useStyles();
	const [Loading, setLoading] = React.useState(false);
	const [selectedCut, setSelectedCut] = React.useState(null);
	let [rows, setRows] = React.useState([]);
	const [orderQuantity, setOrderQuantity] = React.useState(null);
	const [totalCuts, setTotalCuts] = React.useState(null);
	const [headCells, setHeadCells] = React.useState([
		{
			field: "bundle",
			title: "Bundle",
			editable: false,
		},
		{
			field: "quantity",
			title: "Quantity",
			editable: false,
		},
		{
			field: "size",
			title: "Size",
			editable: false,
		},
		{
			field: "special_process",
			title: "Special Process",
			editable: false,
		}
	]);
	React.useEffect(() => {

		quantitySum = 0;

		setLoading(true);
		let userInfo = JSON.parse(localStorage.getItem("userInfo"));
		fetch(
			serverIP +
			forViewgetOrdersFromCutReport +
			"?unitID=" +
			userInfo.unitID
		)
			.then((response) => {
				if (response.ok) {
					return response.json();
				} else {
					throw new Error("Something went wrong");
				}
			})
			.then((data) => {
				setLoading(false);
				try {
					if (data.Error_No === 0) {
						var columnsJSON = [];
						all_progress_complete_data = data.Orders;
						data.Orders.map((order) => {
							var colResult = {};
							var repeated = orders.filter(function(a) {
								return a.value == order.orderID;
							}).length;
							if (!repeated) {
								colResult["value"] = order.orderID;
								colResult["label"] = order.orderID;
								colResult["orderQuantity"] = order.orderQuantity;
								colResult["totalCuts"] = order.totalCuts;
								orders.push(colResult);
							}
						});
					} else {
						NotificationManager.error(
							"Unable to fetch Orders" + data.Error_Description
						);
					}
				} catch (ex) {
					console.log("error");
				}
			})
			.catch((error) => {
				setLoading(false);
				NotificationManager.error(
					"Failed to fetch, connection to server failed"
				);
				console.log(error);
			});
	}, []);
	function get_cuts_for_orders(order) {
		cuts = [];
		setLoading(true);
		fetch(serverIP + forViewgetCutsForOrder + "?" + "orderID=" + order)
			.then((response) => {
				if (response.ok) {
					return response.json();
				} else {
					throw new Error("Something went wrong");
				}
			})
			.then((data) => {
				setLoading(false);
				try {
					if (data.Error_No === 0) {
						var columnsJSON = [];
						data.Cuts.map((cut) => {
							var colResult = {};
							colResult["value"] = cut.cutID;
							colResult["label"] = cut.cutID;
							cuts.push(colResult);
						});
					} else {
						NotificationManager.error(
							"Unable to fetch Cuts" + data.Error_Description
						);
					}
				} catch (ex) {
					console.log("error");
				}
			})
			.catch((error) => {
				setLoading(false);
				NotificationManager.error(
					"Failed to fetch, connection to server failed"
				);
				console.log(error);
			});
	}
	function get_bundles_for_cut_order(order, cut) {
		let bundles = [];
		setLoading(true);
		fetch(
			serverIP +
			forViewgetBundlesForOrderCut +
			"?" +
			"orderID=" +
			order +
			"&&cutID=" +
			cut
		)
			.then((response) => {
				if (response.ok) {
					return response.json();
				} else {
					throw new Error("Something went wrong");
				}
			})
			.then((data) => {
				setLoading(false);
				try {
					if (data.Error_No === 0) {
						var columnsJSON = [];

						data.Bundles.map((bundle) => {
							bundles.push(
								createData(
									bundle.itemID,
									bundle.bundleID,
									bundle.quantity,
									bundle.size,
									bundle.special_process
								)
							);
						});

						data = bundles;
						console.log(data);

						quantitySum = bundles.reduce((sum, current) => sum += parseInt(current.quantity), 0);

						setRows(bundles);
					} else {
						NotificationManager.error(
							"Unable to fetch Bundles" + data.Error_Description
						);
					}
				} catch (ex) {
					console.log("error");
				}
			})
			.catch((error) => {
				setLoading(false);
				NotificationManager.error(
					"Failed to fetch, connection to server failed"
				);
				console.log(error);
			});
	}
	function update_bundle_quantity(itemID, quantity, newData, oldData) {
		setLoading(true);
		fetch(
			serverIP +
			updateBundle +
			"?" +
			"itemID=" +
			itemID +
			"&&quantity=" +
			quantity
		)
			.then((response) => {
				if (response.ok) {
					return response.json();
				} else {
					throw new Error("Something went wrong");
				}
			})
			.then((data) => {
				setLoading(false);
				try {
					if (data.Error_No === "0") {
						var columnsJSON = [];
						NotificationManager.success(
							"Bundle quantity updated successfully"
						);
						let temp = [...rows];
						temp[temp.indexOf(oldData)] = newData;

						quantitySum = temp.reduce((sum, current) => sum += parseInt(current.quantity), 0);
						setRows(temp);
					} else {
						NotificationManager.error(
							"Unable to update bundle quantity" +
							data.Error_Description
						);
					}
				} catch (ex) {
					console.log("error");
				}
			})
			.catch((error) => {
				setLoading(false);
				NotificationManager.error(
					"Failed to fetch, connection to server failed"
				);
				console.log(error);
			});
	}
	const handleOrderDropDownChange = (event) => {
		setRows([]);
		quantitySum = 0;

		if (event != null) {
			selected_order = event.value;
			setOrderQuantity(event.orderQuantity);
			setTotalCuts(event.totalCuts);
			get_cuts_for_orders(event.value);
		} else {
			selected_order = null;
		}
	};
	const handleCutDropDownChange = (event) => {
		setRows([]);
		quantitySum = 0;

		if (event != null) {
			selected_cut = event.value;
			get_bundles_for_cut_order(selected_order, selected_cut);
		} else {
		}
	};

	return (
		<>
			<LoadingOverlay active={Loading} spinner text={"Fetching data"}>
				<div
					style={{
						display: "flex",
						justifyContent: "center",
						padding: "0px 0px 15px",
					}}
				>
					<Typography variant="h5">Bundle Quantity Update</Typography>
				</div>
				<Paper className={classes.paper}>
					<Grid container>
						<Grid item lg={1} md={1} sm={1} xs={1}>
							<Typography
								style={{
									textAlign: "center",
									marginTop: "10px",
								}}
								variant="subtitle2"
							>
								ORDER
							</Typography>
						</Grid>
						<Grid item lg={2} md={2} sm={2} xs={2}>
							<Select
								className="basic-single"
								label="Cut"
								onChange={handleOrderDropDownChange}
								options={orders}
								isSearchable={true}
								components={animatedComponents}
								helperText="Please select your order"
								variant="outlined"
							/>
						</Grid>
						<Grid item lg={1} md={1} sm={1} xs={1}>
							<Typography
								style={{
									textAlign: "center",
									marginTop: "10px",
								}}
								variant="subtitle2"
							>
								CUT
							</Typography>
						</Grid>
						<Grid item lg={2} md={2} sm={2} xs={2}>
							<Select
								className="basic-single"
								label="Cut"
								onChange={handleCutDropDownChange}
								options={cuts}
								isSearchable={true}
								isClearable={true}
								components={animatedComponents}
								helperText="Please select your Cut"
								variant="outlined"
							/>
						</Grid>
						{selected_order !== null && (<Grid item lg={2} md={2} sm={2} xs={2}>
							<Typography
								style={{
									textAlign: "center",
									marginTop: "10px",
								}}
								variant="subtitle2"
							>
								Order Quantity:  {orderQuantity}
							</Typography>
						</Grid>)}
						{selected_order !== null && (<Grid item lg={2} md={2} sm={2} xs={2}>
							<Typography
								style={{
									textAlign: "center",
									marginTop: "10px",
								}}
								variant="subtitle2"
							>
								Total No Cuts: {totalCuts}
							</Typography>
						</Grid>)}

						{/* <Grid
                            item
                            lg={5}
                            md={5}
                            sm={5}
                            xs={5}
                            style={{
                                padding: "0px 5px",
                                display: "flex",
                                justifyContent: "flex-end",
                            }}
                        >
                            <Button
                                variant="contained"
                                color="primary"
                                disableElevation
                                style={{
                                    height: "50px",
                                }}
                            >
                                Transfer Scannings
                            </Button>
                        </Grid> */}

						{quantitySum !== 0 &&
							<Grid
								item
								xs={2}
								style={{
									marginLeft: 'auto'
								}}
							>
								<Typography
									style={{
										textAlign: "center",
										marginTop: "10px",
									}}
									variant="subtitle2"
								>
									Total Cut Quantity: {quantitySum}
								</Typography>
							</Grid>
						}
					</Grid>
				</Paper>
				<Paper
					className={classes.paper}
					style={{
						backgroundColor: "#900C3E",
						color: "#fff",
						margin: "10px 10px 10px 10px",
					}}
				>
					<Grid container>
						<Grid item lg={12} md={12} sm={12} xs={12}>
							{rows.length > 0 && (
								<MaterialTableDemo
									headCells={headCells}
									Rows={rows}
									UpdateQuantityFunction={
										update_bundle_quantity
									}
								/>
							)}
						</Grid>
					</Grid>
				</Paper>
			</LoadingOverlay>
		</>
	);
};

export default UpdateQuantity;
