import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Drawer from "@material-ui/core/Drawer";
import MachineImage from "../../assets/images/machines.jpg";
import TransformIcon from "@material-ui/icons/Transform";
import List from "@material-ui/core/List";
import Divider from "@material-ui/core/Divider";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";
import DashboardIcon from "@material-ui/icons/Dashboard";
import { withRouter } from "react-router-dom";
import { drawerWidth } from "../../util/config";
import { ListItemIcon } from "@material-ui/core";
import DeleteSweepIcon from "@material-ui/icons/DeleteSweep";
import RateReviewIcon from "@material-ui/icons/RateReview";
import AssignmentIcon from "@material-ui/icons/Assignment";
import ExpandLess from "@material-ui/icons/ExpandLess";
import ExpandMore from "@material-ui/icons/ExpandMore";
import Collapse from "@material-ui/core/Collapse";
import PeopleOutlineIcon from "@material-ui/icons/PeopleOutline";
import DeveloperBoardIcon from "@material-ui/icons/DeveloperBoard";
import PlaylistAddCheckIcon from "@material-ui/icons/PlaylistAddCheck";

const useStyles = makeStyles((theme) => ({
    root: {
        display: "flex",
    },
    drawer: {
        width: drawerWidth,
        flexShrink: 0,
    },
    drawerPaper: {
        width: drawerWidth,

        color: "white",
        backgroundImage: `linear-gradient(
            rgba(21, 21, 21, 1.85),
            rgba(21, 21, 21, 0.75)
          ),url(${MachineImage})`,
    },
    toolbar: theme.mixins.toolbar,
    content: {
        flexGrow: 1,
        backgroundColor: theme.palette.background.default,
        padding: theme.spacing(3),
    },
}));

const DrawerComponent = (props) => {
    const classes = useStyles();
    //const [selected,setSelected] = React.useState(1)
    const [cutReportMenuOpen, setcutReportMenuOpen] = React.useState(false);
    const [StyleBulletinMenuOpen, setStyleBulletinMenuOpen] = React.useState(
        false
    );
    const [operationsMenuOpen, setoperationsMenuOpen] = React.useState(false);
    const [machinesMenuOpen, setmachinesMenuOpen] = React.useState(false);
    const [workerMenuOpen, setworkerMenuOpen] = React.useState(false);

    const handleCutReportClick = () => {
        setcutReportMenuOpen(!cutReportMenuOpen);
    };
    const handleStyleBulletinClick = () => {
        setStyleBulletinMenuOpen(!StyleBulletinMenuOpen);
    };
    const handleOperationClick = () => {
        setoperationsMenuOpen(!operationsMenuOpen);
    };

    return (
        <Drawer
            className={classes.drawer}
            variant="permanent"
            classes={{
                paper: classes.drawerPaper,
            }}
            anchor="left"
        >
            <div
                style={{
                    height: "150px",
                    padding: "15px",
                    textAlign: "center",
                }}
            >
                <img
                    src={require("../../assets/images/usaparel_white.png")}
                    style={{ height: "110%" }}
                />
            </div>
            {/* <div className={classes.toolbar} />
        <Divider /> */}
            <List>
                <div
                    style={{
                        backgroundColor:
                            props.selected === 1 ? "#900C3E" : "transparent",
                        margin: "10px",
                        borderRadius: "5px",
                    }}
                >
                    <ListItem
                        button
                        key={"dashboard"}
                        onClick={() => props.setSelected(1)}
                    >
                        <ListItemIcon>
                            <DashboardIcon style={{ color: "#fff" }} />
                        </ListItemIcon>
                        <ListItemText primary={`Dashboard`} />
                    </ListItem>
                </div>

                <div
                    style={{
                        backgroundColor:
                            props.selected === 2 ? "#900C3E" : "transparent",
                        margin: "10px",
                        borderRadius: "5px",
                    }}
                >
                    <ListItem
                        button
                        key={"orderTransfer"}
                        onClick={() => props.setSelected(2)}
                    >
                        <ListItemIcon>
                            <TransformIcon style={{ color: "#fff" }} />
                        </ListItemIcon>
                        <ListItemText primary={`Order Transfer`} />
                    </ListItem>
                </div>

                <div
                    style={{
                        backgroundColor:
                            props.selected === 3 ? "#900C3E" : "transparent",
                        margin: "10px",
                        borderRadius: "5px",
                    }}
                >
                    <ListItem
                        button
                        key={"orderTransfer"}
                        onClick={() => props.setSelected(3)}
                    >
                        <ListItemIcon>
                            <DeleteSweepIcon style={{ color: "#fff" }} />
                        </ListItemIcon>
                        <ListItemText primary={"Delete Scanning"} />
                    </ListItem>
                </div>

                <div
                    style={{
                        margin: "10px",
                        borderRadius: "5px",
                    }}
                >
                    {/* <ListItem button onClick={() => props.setSelected(4)}>
                        <ListItemIcon>
                            <UpdateIcon style={{ color: "#fff" }} />
                        </ListItemIcon>
                        <ListItemText primary={"Quantity Update"} />
                    </ListItem> */}

                    <ListItem button onClick={handleCutReportClick}>
                        <ListItemIcon>
                            <AssignmentIcon style={{ color: "#fff" }} />
                        </ListItemIcon>
                        <ListItemText primary="Cut Report" />
                        {cutReportMenuOpen ? <ExpandLess /> : <ExpandMore />}
                    </ListItem>
                    <Collapse
                        in={cutReportMenuOpen}
                        timeout="auto"
                        unmountOnExit
                    >
                        <List component="div" disablePadding>
                            <ListItem
                                button
                                style={{
                                    backgroundColor:
                                        props.selected === 7
                                            ? "#900C3E"
                                            : "transparent",
                                    margin: "10px",
                                    borderRadius: "5px",
                                }}
                                onClick={() => props.setSelected(7)}
                                className={classes.nested}
                            >
                                <ListItemIcon>
                                    <AssignmentIcon style={{ color: "#fff" }} />
                                </ListItemIcon>
                                <ListItemText primary="Upload Cut Report" />
                            </ListItem>

                            <ListItem
                                button
                                style={{
                                    backgroundColor:
                                        props.selected === 4
                                            ? "#900C3E"
                                            : "transparent",
                                    margin: "10px",
                                    borderRadius: "5px",
                                }}
                                onClick={() => props.setSelected(4)}
                                className={classes.nested}
                            >
                                <ListItemIcon>
                                    <RateReviewIcon style={{ color: "#fff" }} />
                                </ListItemIcon>
                                <ListItemText primary="View Cut Report" />
                            </ListItem>
                        </List>
                    </Collapse>
                </div>

                <div
                    style={{
                        margin: "10px",
                        borderRadius: "5px",
                    }}
                >
                    <ListItem button onClick={handleStyleBulletinClick}>
                        <ListItemIcon>
                            <AssignmentIcon style={{ color: "#fff" }} />
                        </ListItemIcon>
                        <ListItemText primary="Style Bulletin" />
                        {StyleBulletinMenuOpen ? (
                            <ExpandLess />
                        ) : (
                            <ExpandMore />
                        )}
                    </ListItem>
                    <Collapse
                        in={StyleBulletinMenuOpen}
                        timeout="auto"
                        unmountOnExit
                    >
                        <List component="div" disablePadding>
                            <ListItem
                                button
                                style={{
                                    backgroundColor:
                                        props.selected === 6
                                            ? "#900C3E"
                                            : "transparent",
                                    margin: "10px",
                                    borderRadius: "5px",
                                }}
                                onClick={() => props.setSelected(6)}
                                className={classes.nested}
                            >
                                <ListItemIcon>
                                    <AssignmentIcon style={{ color: "#fff" }} />
                                </ListItemIcon>
                                <ListItemText primary="Upload Style Bulletin" />
                            </ListItem>

                            <ListItem
                                button
                                style={{
                                    backgroundColor:
                                        props.selected === 5
                                            ? "#900C3E"
                                            : "transparent",
                                    margin: "10px",
                                    borderRadius: "5px",
                                }}
                                onClick={() => props.setSelected(5)}
                                className={classes.nested}
                            >
                                <ListItemIcon>
                                    <RateReviewIcon style={{ color: "#fff" }} />
                                </ListItemIcon>
                                <ListItemText primary="View Style Bulletin" />
                            </ListItem>
                        </List>
                    </Collapse>
                </div>

                <div
                    style={{
                        margin: "10px",
                        borderRadius: "5px",
                    }}
                >
                    <ListItem
                        button
                        onClick={() => setmachinesMenuOpen(!machinesMenuOpen)}
                    >
                        <ListItemIcon>
                            <DeveloperBoardIcon style={{ color: "#fff" }} />
                        </ListItemIcon>
                        <ListItemText primary="Machines" />
                        {machinesMenuOpen ? <ExpandLess /> : <ExpandMore />}
                    </ListItem>
                    <Collapse
                        in={machinesMenuOpen}
                        timeout="auto"
                        unmountOnExit
                    >
                        <List component="div" disablePadding>
                            <ListItem
                                button
                                style={{
                                    backgroundColor:
                                        props.selected === 12
                                            ? "#900C3E"
                                            : "transparent",
                                    margin: "10px",
                                    borderRadius: "5px",
                                }}
                                onClick={() => props.setSelected(12)}
                                className={classes.nested}
                            >
                                <ListItemIcon>
                                    <AssignmentIcon style={{ color: "#fff" }} />
                                </ListItemIcon>
                                <ListItemText primary="Add Machines" />
                            </ListItem>

                            <ListItem
                                button
                                style={{
                                    backgroundColor:
                                        props.selected === 8
                                            ? "#900C3E"
                                            : "transparent",
                                    margin: "10px",
                                    borderRadius: "5px",
                                }}
                                onClick={() => props.setSelected(8)}
                                className={classes.nested}
                            >
                                <ListItemIcon>
                                    <PlaylistAddCheckIcon
                                        style={{ color: "#fff" }}
                                    />
                                </ListItemIcon>
                                <ListItemText primary="View Machines" />
                            </ListItem>
                        </List>
                    </Collapse>
                </div>

                <div
                    style={{
                        margin: "10px",
                        borderRadius: "5px",
                    }}
                >
                    <ListItem button onClick={handleOperationClick}>
                        <ListItemIcon>
                            <AssignmentIcon style={{ color: "#fff" }} />
                        </ListItemIcon>
                        <ListItemText primary="Operations" />
                        {operationsMenuOpen ? <ExpandLess /> : <ExpandMore />}
                    </ListItem>
                    <Collapse
                        in={operationsMenuOpen}
                        timeout="auto"
                        unmountOnExit
                    >
                        <List component="div" disablePadding>
                            <ListItem
                                button
                                style={{
                                    backgroundColor:
                                        props.selected === 11
                                            ? "#900C3E"
                                            : "transparent",
                                    margin: "10px",
                                    borderRadius: "5px",
                                }}
                                onClick={() => props.setSelected(11)}
                                className={classes.nested}
                            >
                                <ListItemIcon>
                                    <AssignmentIcon style={{ color: "#fff" }} />
                                </ListItemIcon>
                                <ListItemText primary="Upload Operations" />
                            </ListItem>

                            <ListItem
                                button
                                style={{
                                    backgroundColor:
                                        props.selected === 9
                                            ? "#900C3E"
                                            : "transparent",
                                    margin: "10px",
                                    borderRadius: "5px",
                                }}
                                onClick={() => props.setSelected(9)}
                                className={classes.nested}
                            >
                                <ListItemIcon>
                                    <PlaylistAddCheckIcon
                                        style={{ color: "#fff" }}
                                    />
                                </ListItemIcon>
                                <ListItemText primary="View Operations" />
                            </ListItem>
                        </List>
                    </Collapse>
                </div>

                {/* <div
                    style={{
                        backgroundColor:
                            props.selected === 9 ? "#900C3E" : "transparent",
                        margin: "10px",
                        borderRadius: "5px",
                    }}
                >
                    <ListItem button onClick={() => props.setSelected(9)}>
                        <ListItemIcon>
                            <PlaylistAddCheckIcon style={{ color: "#fff" }} />
                        </ListItemIcon>
                        <ListItemText primary={"Operation"} />
                    </ListItem>
                </div> */}

                <div
                    style={{
                        margin: "10px",
                        borderRadius: "5px",
                    }}
                >
                    <ListItem
                        button
                        onClick={() => setworkerMenuOpen(!workerMenuOpen)}
                    >
                        <ListItemIcon>
                            <DeveloperBoardIcon style={{ color: "#fff" }} />
                        </ListItemIcon>
                        <ListItemText primary="Workers" />
                        {workerMenuOpen ? <ExpandLess /> : <ExpandMore />}
                    </ListItem>
                    <Collapse in={workerMenuOpen} timeout="auto" unmountOnExit>
                        <List component="div" disablePadding>
                            <ListItem
                                button
                                style={{
                                    backgroundColor:
                                        props.selected === 13
                                            ? "#900C3E"
                                            : "transparent",
                                    margin: "10px",
                                    borderRadius: "5px",
                                }}
                                onClick={() => props.setSelected(13)}
                                className={classes.nested}
                            >
                                <ListItemIcon>
                                    <AssignmentIcon style={{ color: "#fff" }} />
                                </ListItemIcon>
                                <ListItemText primary="Upload Workers" />
                            </ListItem>

                            <ListItem
                                button
                                style={{
                                    backgroundColor:
                                        props.selected === 10
                                            ? "#900C3E"
                                            : "transparent",
                                    margin: "10px",
                                    borderRadius: "5px",
                                }}
                                onClick={() => props.setSelected(10)}
                                className={classes.nested}
                            >
                                <ListItemIcon>
                                    <PlaylistAddCheckIcon
                                        style={{ color: "#fff" }}
                                    />
                                </ListItemIcon>
                                <ListItemText primary="View Workers" />
                            </ListItem>
                        </List>
                    </Collapse>
                </div>

                <div
                    style={{
                        backgroundColor:
                            props.selected === 14 ? "#900C3E" : "transparent",
                        margin: "10px",
                        borderRadius: "5px",
                    }}
                >
                    <ListItem
                        button
                        key={"CutBundleCreation"}
                        onClick={() => props.setSelected(14)}
                    >
                        <ListItemIcon>
                            <TransformIcon style={{ color: "#fff" }} />
                        </ListItemIcon>
                        <ListItemText primary={`Cut/Bundle Creation`} />
                    </ListItem>
                </div>

				<div
                    style={{
                        backgroundColor:
                            props.selected === 15 ? "#900C3E" : "transparent",
                        margin: "10px",
                        borderRadius: "5px",
                    }}
                >
                    <ListItem
                        button
                        key={"Users"}
                        onClick={() => props.setSelected(15)}
                    >
                        <ListItemIcon>
                            <TransformIcon style={{ color: "#fff" }} />
                        </ListItemIcon>
                        <ListItemText primary={`Users`} />
                    </ListItem>
                </div>
				<div
                    style={{
                        backgroundColor:
                            props.selected === 16 ? "#900C3E" : "transparent",
                        margin: "10px",
                        borderRadius: "5px",
                    }}
                >
                    <ListItem
                        button
                        key={"Transfer Scannings"}
                        onClick={() => props.setSelected(16)}
                    >
                        <ListItemIcon>
                            <TransformIcon style={{ color: "#fff" }} />
                        </ListItemIcon>
                        <ListItemText primary={`Transfer Scannings`} />
                    </ListItem>
                </div>
            </List>



			<div
                style={{ height: "80px", padding: "15px", textAlign: "center" }}
            >
                <img
                    src={require("../../assets/images/WiMetrix4.png")}
					alt="Wimetrix logo"
                    style={{ height: "90%" }}
                />
            </div>


            {/* <Divider /> */}
        </Drawer>
    );
};

export default withRouter(DrawerComponent);
