import MUIDataTable from "mui-datatables";
import React from "react";
const Datatable = (props) => {
	const columns = [
		{
			name: "Order_code",
			label: "Order Code",
			options: {
				filter: true,
				sort: false,
			}
		},
		{
			name: "Scanning_time",
			label: "Scan Time",
			options: {
				filter: false,
				sort: true,
			}
		},
		{
			name: "lineDescription",
			label: "Line Description",
			options: {
				filter: false,
				sort: false,
			}
		},
		{
			name: "operationDescription",
			label: "Operation Description",
			options: {
				filter: true,
				sort: false,
			}
		},
		{
			name: "sectionDescription",
			label: "Section Description",
			options: {
				filter: false,
				sort: false,
			}
		},
		{
			name: "workerName",
			label: "Worker Name",
			options: {
				filter: true,
				sort: false,
			}
		},
		{
			name: "cutID",
			label: "Cut",
			options: {
				filter: true,
				sort: false,
			}
		},
		{
			name: "bundleID",
			label: "Bundle Number",
			options: {
				filter: true,
				filterType:"multiselect",
				sort: false,
			}
		},
	];
	const data = props.Rows;
	const options = {
		filterType: 'dropdown',
		onRowsDelete: (rowsDeleted) => {
			return false;
		},
		onRowSelectionChange: (currentRowsSelected,allRowsSelected,rowsSelected) => {
			console.log(allRowsSelected)
			let selectedRows = [];
			if(allRowsSelected.length){
				allRowsSelected.map((index) => {
					console.log(data[index.dataIndex]);
					selectedRows.push(data[index.dataIndex].Bundle_scan_id);
				});
			}
			console.log(selectedRows)
			props.setSelectedRows(selectedRows);
		},
		toolbar: {
			search: "Search",
			download:false,
			downloadCsv: false,
			print: false,
			viewColumns: "View Columns",
			filterTable: "Filter Table",
			delete:false
		},


	};
	return (
		<MUIDataTable
			title={"Bundle scannings"}
			data={data}
			columns={columns}
			options={options}
		/>
	);
};

export default (Datatable);