import React from "react";
import { Paper, Typography, Grid, TextField } from "@material-ui/core";
import Autocomplete from '@material-ui/lab/Autocomplete';
import { green } from "@material-ui/core/colors";
import { NotificationManager } from "react-notifications";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import makeAnimated from "react-select/animated";
import Select from "react-select";
import Button from "@material-ui/core/Button";
import LoadingOverlay from "react-loading-overlay";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import useMediaQuery from "@material-ui/core/useMediaQuery";
import { serverIP, monthNames } from "../../util/Constants";
import DataTable from "./datatable";
import DateRangePicker from 'react-bootstrap-daterangepicker';
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap-daterangepicker/daterangepicker.css';
import moment from 'moment';
const animatedComponents = makeAnimated();

const useStyles = makeStyles((theme) => ({
	paper: {
		padding: "20px",
	},
	field: {
		//width: '200px'
	},
	greenAvatar: {
		marginRight: "10px",
		color: "#fff",
		backgroundColor: green[500],
	},
	root: {
		width: "100%",

		backgroundColor: theme.palette.background.paper,
		position: "relative",
		overflow: "auto",
		maxHeight: "850px",
	},
	columnPaper: {
		display: "grid",
		gridTemplateColumns: "25% 25% 25% 25%",
		padding: "5px",
		margin: "10px 5px",
	},
}));

let orders = [];
let operations = [];
let selected_operations = [];
let selected_order = null;

const TransferScannings = () => {
	let userInfo = JSON.parse(localStorage.getItem("userInfo"));
	const classes = useStyles();
	const theme = useTheme();
	const [open, setOpen] = React.useState(false);
	const [warningDialogOpen, setWarningDialogOpen] = React.useState(false);
	const [Loading, setLoading] = React.useState(false);
	const [selectedRows, setSelectedRows] = React.useState([]);
	const [workers, setWorkers] = React.useState([]);
	const [selectedWorker, setSelectedWorker] = React.useState(null);
	const [selectedWorkerName, setSelectedWorkerName] = React.useState(null);
	let [rows, setRows] = React.useState([]);
	const fullScreen = useMediaQuery(theme.breakpoints.down("sm"));
	const headCells = [
		{
			field: "Scanning_time",
			title: "Scan Time",
			disablePadding: false,
		},
		{
			field: "lineDescription",
			title: "Line Description",
			disablePadding: false,
		},
		{
			field: "operationDescription",
			title: "Operation Description",
			disablePadding: false,
		},
		{
			field: "sectionDescription",
			title: "Section Description",
			disablePadding: false,
		},
		{
			field: "workerName",
			title: "Worker Name",
			disablePadding: false,
		},
		{
			field: "cutID",
			title: "Cut",
			disablePadding: false,
		},
		{
			field: "bundleID",
			title: "Bundle",
			disablePadding: false,
		}
	];
	React.useEffect(() => {
		get_workers();
	}, []);
	const handleDisagreeDialogClick = () => {
		setOpen(false);
		setWarningDialogOpen(false);
	};
	const handleAgreeDialogClick = () => {
		setOpen(false);
		setWarningDialogOpen(false);
		if (selectedWorker != null) {
			transferScanningRequest(
				selectedWorker,
				selectedRows,
			);
			console.log(selectedWorker);
			console.log(selectedRows);

		} else {
			NotificationManager.error("Select worker");
		}
	};
	const handleDeleteScanningsButtonClick = (selectedWorker, selectedRows) => {
		setOpen(true);
	};
	const transferScanningRequest = async () => {
		setLoading(true);
		await fetch(
			serverIP +
			"UpdateWorkerInBundleScanning/updateWorkerInBundleScannings.php?scan_ids=" +
			"(" + selectedRows.toString()
			+ `)&&worker_id=` + selectedWorker.value
			+ `&&worker_code=` + selectedWorker.code
			+ `&&worker_name=` + selectedWorker.label,
			{
				method: "POST",
			}
		)
			.then((response) => {
				if (response.ok) {
					return response.json();
				} else {
					throw new Error("Something went wrong");
				}
			})
			.then((data) => {
				setLoading(false);
				try {
					if (data.Error_No == "0") {
						var columnsJSON = [];
						setRows([]);
						NotificationManager.success(
							"Scannings transferred successfully"
						);
					} else {
						NotificationManager.error(
							"Unable to transfer Scannings" +
							data.Error_Description
						);
					}
				} catch (ex) {
					console.log("error");
				}
			})
			.catch((error) => {
				console.log(error);
				setLoading(false);
				NotificationManager.error(
					"Failed to fetch, check your server or server file, Unexpected response from server file or the application is unable to reach server file"
				);
			});
	};
	function get_operations_for_orders(order) {
		operations = [];
		setLoading(true);
		fetch(
			serverIP +
			"/DeleteScannings/getCutsForOrderFromProgressComplete.php?" +
			"orderID=" +
			order + "&&unitID=" + userInfo.unitID
		)
			.then((response) => {
				if (response.ok) {
					return response.json();
				} else {
					throw new Error("Something went wrong");
				}
			})
			.then((data) => {
				setLoading(false);
				try {
					if (data.Error_No === 0) {
						data.Operations.map((operation) => {
							var colResult = {};
							colResult["value"] = operation.operationID;
							colResult["label"] = operation.operationID;
							operations.push(colResult);
						});
					} else {
						NotificationManager.error(
							"Unable to fetch Operations" +
							data.Error_Description
						);
					}
				} catch (ex) {
					console.log("error");
				}
			})
			.catch((error) => {
				setLoading(false);
				NotificationManager.error(
					"Failed to fetch, connection to server failed"
				);
				console.log(error);
			});
	}
	function get_scannings_for_date_range(startDate, endDate) {
		setLoading(true);
		fetch(
			serverIP +
			"/UpdateWorkerInBundleScanning/getBundleScannings.php?" +
			"startDate=" +
			startDate +
			"&&endDate=" +
			endDate +
			"&&unitID=" + userInfo.unitID
		)
			.then((response) => {
				if (response.ok) {
					return response.json();
				} else {
					throw new Error("Something went wrong");
				}
			})
			.then((data) => {
				setLoading(false);
				try {
					if (data.Error_No === 0) {
						setRows(data.Scannings);
					} else {
						NotificationManager.error(
							"Unable to fetch scannings " +
							data.Error_Description
						);
					}
				} catch (ex) {
					console.log("error");
				}
			})
			.catch((error) => {
				setLoading(false);
				NotificationManager.error(
					"Failed to fetch, connection to server failed"
				);
				console.log(error);
			});
	}
	function get_workers() {
		setLoading(true);
		fetch(serverIP + "/Workers/getAllWorkers.php?unitID=" + userInfo.unitID)
			.then((response) => {
				if (response.ok) {
					return response.json();
				} else {
					throw new Error("Something went wrong");
				}
			})
			.then((data) => {
				setLoading(false);
				try {
					if (data.Error_No === 0) {
						let workers = [];
						data.Workers.map((worker) => {
							var colResult = {};
							var repeated = workers.filter(function(a) {
								return a.value === worker.workerID;
							}).length;
							if (!repeated) {
								colResult["value"] = worker.workerID;
								colResult["code"] = worker.workerCode;
								colResult["label"] = worker.workerName;
								workers.push(colResult);
							}
						});
						setWorkers(workers);
					} else {
						NotificationManager.error(
							"Unable to fetch workers " +
							data.Error_Description
						);
					}
				} catch (ex) {
					console.log("error");
				}
			})
			.catch((error) => {
				setLoading(false);
				NotificationManager.error(
					"Failed to fetch, connection to server failed"
				);
				console.log(error);
			});
	}
	return (
		<>
			<LoadingOverlay active={Loading} spinner text={"Fetching data"}>
				<div
					style={{
						display: "flex",
						justifyContent: "center",
						padding: "0px 0px 15px",
					}}
				>
					<Typography variant="h5">Bundle Scannings</Typography>
				</div>
				<Paper className={classes.paper}>
					<Grid container>
						<Grid item lg={1} md={1} sm={1} xs={1}>
							<Typography
								style={{
									textAlign: "center",
									marginTop: "10px",
								}}
								variant="subtitle2"
							>
								Date Range
							</Typography>
						</Grid>
						<Grid item lg={4} md={4} sm={4} xs={4}>
							<DateRangePicker
								onApply={(event, picker) => {
									console.log(moment(picker.startDate._d).format('DD-MMM-YYYY'));
									console.log(moment(picker.endDate._d).format('DD-MMM-YYYY'));
									get_scannings_for_date_range(moment(picker.startDate._d).format('DD-MMM-YYYY'), moment(picker.endDate._d).format('DD-MMM-YYYY'));
								}}
								initialSettings={{ startDate: moment(), endDate: moment() }}
							>
								<input type="text" className="form-control" />
							</DateRangePicker>
						</Grid>
						<Grid item lg={1} md={1} sm={1} xs={1}>
						</Grid>
						<Grid item lg={3} md={3} sm={3} xs={3}>
						</Grid>
						<Grid item lg={3} md={3} sm={3} xs={3}
							style={{
								padding: "0px 5px",
								display: "flex",
								justifyContent: "flex-end",
							}}
						>
							{selectedRows.length > 0 && (
								<Button
									variant="contained"
									color="primary"
									onClick={handleDeleteScanningsButtonClick}
								>
									Transfer
								</Button>
							)}
						</Grid>
					</Grid>
				</Paper>
				<Dialog
					PaperProps={{ style: { overflowY: 'visible' } }}
					fullScreen={fullScreen}
					open={open}
					aria-labelledby="form-dialog-title"
				>
					<DialogTitle id="form-dialog-title">
						{"Select Worker"}
					</DialogTitle>
					<DialogContent style={{ overflowY: 'visible', width: "600px" }}>
						<Grid container display="flex" style={{ justifyContent: "space-around" }} >
							<Grid item lg={1} md={1} sm={1} xs={1}>
								<Typography
									style={{
										textAlign: "center",
										marginTop: "10px",
									}}
									variant="subtitle2"
								>
									WORKER
								</Typography>
							</Grid>
							<Grid item lg={5} md={5} sm={5} xs={5}>
								<Autocomplete
									PaperProps={{ style: { overflowX: 'visible' } }}
									id="combo-box-demo"
									options={workers}
									getOptionLabel={(option) => option.label}
									onChange={(e, option) => {
										console.log('option ===================================================> ', option);
										if (option) {
											setSelectedWorker(option);
											setSelectedWorkerName(option.label);
										}
										else {
											setSelectedWorker(null);
											setSelectedWorkerName("Not Selected");
										}
									}}
									renderInput={(params) => <TextField style={{ width: "100%" }} {...params} variant="outlined" />}
								/>
							</Grid>
						</Grid>
					</DialogContent>
					<DialogActions>
						<Button
							autoFocus
							onClick={() => setOpen(false)}
							color="primary"
						>
							Disagree
						</Button>
						<Button
							onClick={() => setWarningDialogOpen(true)}
							color="primary"
							autoFocus
						>
							Agree
						</Button>
					</DialogActions>
				</Dialog>
				<Dialog
					PaperProps={{ style: { overflowY: 'visible' } }}
					fullScreen={fullScreen}
					open={warningDialogOpen}
					aria-labelledby="form-dialog-title"
				>
					<DialogTitle id="form-dialog-title">
						{"Are you sure you want to transfer " + selectedRows.length + "bundles to " + selectedWorkerName + " ?"}
					</DialogTitle>
					<DialogActions>
						<Button
							autoFocus
							onClick={handleDisagreeDialogClick}
							color="primary"
						>
							Disagree
						</Button>
						<Button
							onClick={handleAgreeDialogClick}
							color="primary"
							autoFocus
						>
							Agree
						</Button>
					</DialogActions>
				</Dialog>
				<Paper
					className={classes.paper}
					style={{
						backgroundColor: "#900C3E",
						color: "#fff",
						margin: "10px 10px 10px 10px",
					}}
				>
					<Grid container>
						<Grid item lg={12} md={12} sm={12} xs={12}>
							{rows.length > 0 && (
								<DataTable
									headCells={headCells}
									Rows={rows}
									selectedRows={selectedRows}
									setSelectedRows={setSelectedRows}
									handleDeleteScanningsButtonClick={handleDeleteScanningsButtonClick}
								/>
							)}
						</Grid>
					</Grid>
				</Paper>
			</LoadingOverlay>
		</>
	);
};

export default TransferScannings;
