import React from "react";
import Layout from "../../components/Layout";
import Dashboard from "../Dashboard";
import OrderTransfer from "../OrderTransfer";
import UpdateQuantity from "../CutReport/UpdateQuantity/UpdateQuantity";
import DeleteScannings from "../DeleteScannings/DeleteScannings";
import RateAdjustment from "../StyleBulletin/RateAdjustment/RateAdjustment";
import UploadStyleBulletin from "../StyleBulletin/UploadStyleBulletin/UploadStyleBulletin";
import UploadCutReport from "../CutReport/Upload Cutreport/UploadCutReport";

import Machine from "../Machine/Machine";
import Operation from "../Operation/Operation";
import AddOperation from "../Operation/Add Operations/AddOperations";
import AddMachineForm from "../Machine/Add Machines/AddMachineForm";
import Worker from "../Worker/Worker";
import Addworker from "../Worker/Add Workers/AddWorkers";
import CutBundleCreation from "../CutBundleCreation/CutBundleCreation";
import UserAccounts from "../../components/UserAccounts/Add"
import TransferScannigns from "../../components/TransferScannings"

const Home = (props) => {
	const [selected, setSelected] = React.useState(1);
	console.log(props);
	return (
		<Layout selected={selected} setSelected={setSelected}>
			{selected === 1 ? (
				<Dashboard />
			) : selected === 2 ? (
				<OrderTransfer />
			) : selected === 4 ? (
				<UpdateQuantity />
			) : selected === 3 ? (
				<DeleteScannings />
			) : selected === 5 ? (
				<RateAdjustment />
			) : selected === 6 ? (
				<UploadStyleBulletin />
			) : selected === 7 ? (
				<UploadCutReport />
			) : selected === 8 ? (
				<Machine />
			) : selected === 9 ? (
				<Operation />
			) : selected === 10 ? (
				<Worker />
			) : selected === 11 ? (
				<AddOperation />
			) : selected === 12 ? (
				<AddMachineForm />
			) : selected === 13 ? (
				<Addworker />
			) : selected === 14 ? (
				<CutBundleCreation />
			): selected === 15 ? (
				<UserAccounts />
			):  selected === 16 ? (
				<TransferScannigns />
			) : (
				<div style={{ padding: "20px" }}>No option selected.</div>
			)}
		</Layout>
	);
};

export default Home;
