import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import Select from "react-select";
import makeAnimated from "react-select/animated";

const animatedComponents = makeAnimated();
const useStyles = makeStyles({
	table: {
		minWidth: 650,
	},
});

export default function SimpleTable(props) {
	const classes = useStyles();
	const rows = props.data;
	let [totalSMV, setTotalSMV] = React.useState(0);
	let [totalPieceRate, setTotalPieceRate] = React.useState(0);

	let [operations, setOperations] = React.useState(props.operations);

	const handleOperationChange = (event, row) => {
		if (row.majorOperation === "") {
			row.majorOperation = event;
		} else {
			let temp = operations;
			temp.push(row.majorOperation);
			console.log(row.majorOperation);
			setOperations(temp);
			row.majorOperation = event;
		}

		setOperations(
			operations.filter(function(a) {
				return a.value !== event.value;
			})
		);
	};

	React.useEffect(() => {
		let smv = 0;
		let pieceRate = 0;

		if (rows) {
			rows.map((item) => {
				smv = parseFloat(item.SMV) + parseFloat(smv);
				pieceRate = parseFloat(item.pieceRate) + parseFloat(pieceRate);
			});
			setTotalPieceRate(parseFloat(pieceRate).toFixed(2));
			setTotalSMV(parseFloat(smv).toFixed(2));
		}
	}, []);

	return (
		<Table className={classes.table} aria-label="simple table">
			<TableHead>
				<TableRow>
					<TableCell>Order ID</TableCell>
					<TableCell align="center">Minor Operation ID</TableCell>
					<TableCell align="center">Minor Operation Code</TableCell>
					<TableCell align="center">SMV</TableCell>
					<TableCell align="center">Piece Rate</TableCell>
					<TableCell align="center">Section ID</TableCell>
					<TableCell align="center">Section Name</TableCell>
					<TableCell align="center">Operation Sequence</TableCell>
					<TableCell align="center">SL</TableCell>
					<TableCell align="center">Major </TableCell>
					<TableCell align="center">Operation</TableCell>

				</TableRow>
			</TableHead>
			<TableBody>
				{rows.map((row) => (
					<TableRow key={row.OC}>
						<TableCell component="th" scope="row">
							{row.OC}
						</TableCell>
						<TableCell align="center">{row.OP}</TableCell>
						<TableCell align="center">{row.OP_NAME}</TableCell>
						<TableCell align="center">{row.SMV}</TableCell>
						<TableCell align="center">{row.pieceRate}</TableCell>
						<TableCell align="center">{row.SECTION_ID}</TableCell>
						<TableCell align="center">{row.SECTION_NAME}</TableCell>
						<TableCell align="center">{row.OP_SEQ}</TableCell>
						<TableCell align="center">{row.SL}</TableCell>
						<TableCell colSpan={2} align="center">
							{" "}
							<Select
								className="basic-single"
								label="Cut"
								options={operations}
								component={Paper}
								isSearchable={true}
								onChange={(e) => handleOperationChange(e, row)}
								components={animatedComponents}
								helperText="Please select your operation"
								variant="outlined"
							/>
						</TableCell>
					</TableRow>
				))}
				<TableRow>
					<TableCell colSpan={3} align="center">
						TOTAL
                    </TableCell>
					<TableCell align="center">{totalSMV}</TableCell>
					<TableCell align="center">{totalPieceRate}</TableCell>
					<TableCell colSpan={5} align="center"></TableCell>
				</TableRow>
			</TableBody>
		</Table>
	);
}
